import React from 'react';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useInputHandlers } from 'hooks/useAddressInputHandlers';

import { useFullFloor } from './AddressFullFloor.hooks';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullFloor = () => {
    const { handleInputSubmit, handleInputFocus } = useInputHandlers();

    const { inputValue, handleInputChange } = useFullFloor();

    return (
        <StyledInput
            title="Этаж"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
            onInputFocus={handleInputFocus}
        />
    );
};
