import { CatalogListItemViewProps } from 'pages/Catalog/ListItem/CatalogListItem@sberbox';
import { AppState } from 'store/constants';
import { selectCartItemById, selectProductById, selectProductDiffById } from 'store/selectors';

export const formatPrice = (price: number) => Math.ceil(price / 100);

export const prepareCount = (state: AppState, itemId: string) => {
    const cartItem = selectCartItemById(state, itemId);

    return cartItem ? cartItem.count : 0;
};

export const calculateButtonDisabled = (state: AppState, itemId: string) => {
    const productItem = selectProductById(state, itemId);
    const cartItem = selectCartItemById(state, itemId);

    return cartItem && cartItem.count === productItem.quantityLeft;
};

export const getQuantityLeftById = (state: AppState, itemId: string) => {
    const productItem = selectProductById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);

    if (productItemDiff && typeof productItemDiff.quantityLeft !== 'undefined') {
        return productItemDiff.quantityLeft;
    }

    return productItem.quantityLeft;
};

export const getDisabled = (state: AppState, itemId: string) => {
    const quantityLeft = getQuantityLeftById(state, itemId);

    return quantityLeft === 0;
};

export const getPriceById = (state: AppState, itemId: string) => {
    const productItem = selectProductById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);

    if (productItemDiff && typeof productItemDiff.price !== 'undefined') {
        return productItemDiff.price;
    }

    return productItem.price;
};

export const preparePriceById = (state: AppState, itemId: string) => {
    const price = getPriceById(state, itemId);

    return formatPrice(price);
};

export const getCountById = (state: AppState, itemId: string) => {
    const cartItem = selectCartItemById(state, itemId);
    const countInCart = cartItem ? cartItem.count : 0;
    const quantityLeft = getQuantityLeftById(state, itemId);

    return Math.min(countInCart, quantityLeft);
};

export const getOldPriceById = (state: AppState, itemId: string) => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const count = getCountById(state, itemId);
    const productItem = selectProductById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);

    if (quantityLeft === 0 || quantityLeft === count) {
        return undefined;
    }

    if (productItemDiff?.price !== undefined) {
        if (productItemDiff.originalPrice !== undefined && productItemDiff.price < productItemDiff.originalPrice) {
            return formatPrice(productItemDiff.originalPrice);
        }

        if (productItemDiff.price < productItem.price) {
            return formatPrice(productItem.price);
        }
    }

    if (productItem.originalPrice !== undefined && productItem.price < productItem.originalPrice) {
        return formatPrice(productItem.originalPrice);
    }

    return undefined;
};

export const formatSale = (oldValue: number, newValue: number) =>
    String(Math.ceil(((oldValue - newValue) / oldValue) * 100));

export const wantMoreThanHave = (state: AppState, itemId: string) => {
    const cartItem = selectCartItemById(state, itemId);
    const quantityLeft = getQuantityLeftById(state, itemId);

    return cartItem && cartItem.count > quantityLeft;
};

export const canAddMore = (state: AppState, itemId: string) => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const cartItem = selectCartItemById(state, itemId);

    return (
        quantityLeft !== 0 && // есть остатки
        (!cartItem || cartItem.count < quantityLeft) // текущее кол-во меньше остатков
    );
};

export const prepareBadge = (state: AppState, itemId: string): CatalogListItemViewProps['badge'] => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const count = getCountById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);
    const productItem = selectProductById(state, itemId);

    // раскупили
    if (quantityLeft === 0) {
        return { type: 'sold-out' };
    }

    // больше не осталось
    if (count === quantityLeft) {
        return { type: 'few-left' };
    }

    // изменение цены
    if (productItemDiff && typeof productItemDiff.price !== 'undefined') {
        // подорожание
        if (productItemDiff.price > productItem.price) {
            return { type: 'price-changed' };
        }

        // скидка относительно новой цены без скидки
        if (productItemDiff.originalPrice !== undefined && productItemDiff.price < productItemDiff.originalPrice) {
            const content = formatSale(productItemDiff.originalPrice, productItemDiff.price);
            return { type: 'sale', content };
        }

        // скидка относительно старой цены со скидкой
        if (productItemDiff.price < productItem.price) {
            const content = formatSale(productItem.price, productItemDiff.price);
            return { type: 'sale', content };
        }
    }

    // скидка относительно старой цены без скидки
    if (productItem.originalPrice !== undefined && productItem.price < productItem.originalPrice) {
        const content = formatSale(productItem.originalPrice, productItem.price);
        return { type: 'sale', content };
    }

    return undefined;
};
