import React from 'react';
import styled from 'styled-components';
import { Caption } from '@sberdevices/plasma-styles';
import { background, buttonAccent, primary, warning } from '@sberdevices/plasma-tokens';

const captionLineHeightPx = 32;

export type BadgeType = 'sold-out' | 'few-left' | 'sale' | 'price-changed';

const contentMap: Record<BadgeType, (s?: string) => string> = {
    'sold-out': () => 'Раскупили',
    'few-left': () => 'Больше нет',
    'price-changed': () => 'Изменилась цена',
    sale: (value) => `Скидка ${value}%`,
};

const backgroundColorMap: Record<BadgeType, string> = {
    'sold-out': background,
    'few-left': warning,
    'price-changed': warning,
    sale: buttonAccent,
};

const badgePaddingYPx = 8;
const badgeHeightPx = captionLineHeightPx + badgePaddingYPx * 2;
const badgeBorderRadiusPx = badgeHeightPx / 2;

interface StyledBadgeProps {
    type: BadgeType;
}
const StyledBadge = styled(Caption)<StyledBadgeProps>`
    padding: ${badgePaddingYPx}px 16px;
    border-radius: ${badgeBorderRadiusPx}px;

    color: ${primary};
    background-color: ${({ type }) => backgroundColorMap[type]};
`;

export interface BadgeProps extends React.HTMLAttributes<HTMLDivElement> {
    className?: string;
    type: BadgeType;
    content?: string;
}
export const Badge: React.FC<BadgeProps> = ({ type, content, className }) => {
    const contentProvider = contentMap[type];

    return (
        <StyledBadge className={className} type={type}>
            {contentProvider(content)}
        </StyledBadge>
    );
};
