import { Slide } from 'pure-react-carousel';
import React from 'react';
import styled from 'styled-components';

import { CategoriesListItem } from '../ListItem/CategoriesListItem';

const StyledSlide = styled(Slide)`
    display: flex;
    justify-content: center;
    align-items: center;

    transition: margin 0.8s ease;

    will-change: margin;
`;

interface CategoriesSlideProps {
    index: number;
    itemId: string;
}

export const CategoriesSlide: React.FC<CategoriesSlideProps> = ({ index, itemId }) => {
    return (
        <StyledSlide index={index}>
            <CategoriesListItem id={itemId} />
        </StyledSlide>
    );
};
