import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressSubmitThunk } from 'store/thunks/native';
import { addressSelectSuggestItemAction } from 'store/actions/addressSimplePage';
import { selectIsAddressSubmitting } from 'pages/AddressSimple/AddressSimple.selectors';
import { buildAddressString } from 'pages/AddressSimple/AddressSimple.tools';

export function useAddressName(suggestItemId: string) {
    const suggest = useSelector(({ twoGisSuggestsData: { itemsMap } }) => itemsMap[suggestItemId]);

    if (!suggest) {
        return;
    }

    return buildAddressString(suggest);
}

export function useSuggestItemSelected(suggestItemId: string) {
    const selectedSuggestItemId = useSelector(({ addressSimplePage }) => addressSimplePage.selectedSuggestItemId);

    return selectedSuggestItemId === suggestItemId;
}

export function useIsItemDisabled(suggestItemId: string) {
    const isAddressSubmitting = useSelector(selectIsAddressSubmitting);
    const isSelectedItem = useSuggestItemSelected(suggestItemId);

    return isAddressSubmitting && !isSelectedItem;
}

export function useIsItemFlashing(suggestItemId: string) {
    const isAddressSubmitting = useSelector(selectIsAddressSubmitting);
    const isSelectedItem = useSuggestItemSelected(suggestItemId);

    return isAddressSubmitting && isSelectedItem;
}

export function useHandleClick() {
    const dispatch = useDispatch();
    const isAddressSubmitting = useSelector(selectIsAddressSubmitting);

    return React.useCallback(() => {
        if (isAddressSubmitting) {
            return;
        }

        dispatch(addressSubmitThunk());
    }, [dispatch, isAddressSubmitting]);
}

export function useHandleFocus(suggestItemId: string) {
    const dispatch = useDispatch();

    return React.useCallback(() => {
        dispatch(addressSelectSuggestItemAction(suggestItemId));
    }, [dispatch, suggestItemId]);
}
