import React from 'react';
import styled from 'styled-components';
import { surfaceLiquid01, surfaceLiquid02 } from '@sberdevices/plasma-tokens';
import { Skeleton } from 'components/Skeleton/Skeleton';
import { layout } from 'style/const/layout@portal';

import { catalogCardGap } from '../List/CatalogList@portal';
import { cardWidthPx, cardHeightPx } from '../ListItem/CatalogListItem@portal';

const columnCount = Math.floor((layout.width + catalogCardGap) / (cardWidthPx + catalogCardGap)) + 1;

const getWidthByCount = (count: number) => cardWidthPx * count + catalogCardGap * (count - 1);

const skeletonWidth = getWidthByCount(columnCount);

const skeletonPaddingX = (layout.width - getWidthByCount(columnCount - 1)) / 2;
const skeletonPaddingY = (layout.contentHeight - cardWidthPx) / 2 - 75;

const backgroundColor = surfaceLiquid01;
const foregroundColor = surfaceLiquid02;

const StyledRoot = styled.div`
    box-sizing: border-box;
    height: ${layout.contentHeight}px;
    padding-left: ${skeletonPaddingX}px;
    padding-top: ${skeletonPaddingY}px;
`;

export const CatalogSkeletonPortal: React.FC = () => (
    <StyledRoot>
        <Skeleton
            width={skeletonWidth}
            height={cardHeightPx}
            backgroundColor={backgroundColor}
            foregroundColor={foregroundColor}
            itemWidth={cardWidthPx}
            itemHeight={cardHeightPx}
            borderRadius={40}
            rowCount={1}
            columnCount={columnCount}
            paddingBetweenRows={catalogCardGap}
            paddingBetweenColumns={catalogCardGap}
            speed={2}
        />
    </StyledRoot>
);
