import React from 'react';
import {
    DialogPagePortal,
    DialogTitlePortal,
    DialogButtonPortal,
    DialogTextPortal,
} from 'components/DialogPage/DialogPage@portal';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonProps } from 'components/Button/Button';
import { ErrorType } from 'store/constants/error';

import { ErrorPropsMap, mapErrorTypeToPropsCommon, useErrorPage } from './Error.hooks';
import { ErrorPageDecorationPortal } from './Decoration/ErrorPageDecoration@portal';

const mapErrorTypeToProps: ErrorPropsMap = {
    ...mapErrorTypeToPropsCommon,
    [ErrorType.STATUS]: {
        ...mapErrorTypeToPropsCommon[ErrorType.STATUS],
        title: 'Не удалось показать\n статус заказа',
        text: 'Пожалуйста, попробуйте ещё раз',
    },
    [ErrorType.ORDER_PARTNER]: {
        ...mapErrorTypeToPropsCommon[ErrorType.ORDER_PARTNER],
        title: 'Не получилось\n оформить заказ',
        text: 'Пожалуйста, попробуйте ещё раз',
    },
    [ErrorType.ORDER_TIMING]: {
        ...mapErrorTypeToPropsCommon[ErrorType.ORDER_TIMING],
        title: 'Доставка работает\n с 8:00 до 23:00',
        text: 'К сожалению, сейчас нельзя\n оформить заказ',
    },
};

const renderButton = (props: ButtonProps) => <DialogButtonPortal {...props} />;

export const ErrorPortal: React.FC = () => {
    const { title, text, buttons } = useErrorPage({ mapErrorTypeToProps, renderButton });

    return (
        <DialogPagePortal buttons={buttons} decorations={<ErrorPageDecorationPortal />}>
            {title ? (
                <DialogTitlePortal>
                    <MultilineText text={title} />
                </DialogTitlePortal>
            ) : null}
            {text ? (
                <DialogTextPortal>
                    <MultilineText text={text} />
                </DialogTextPortal>
            ) : null}
        </DialogPagePortal>
    );
};
