import { go } from 'connected-react-router';

export const goBackToStart = () => {
    const delta =
        // prettier-ignore
        (
            window.history.length
            // потому что мы должны прийти в 1, а не в 0; у history.length не может быть 0
            - 1
        )
        // мы считаем отрицательную дельту, чтобы снавигироваться назад
        * -1;

    return go(delta);
};
