import React from 'react';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useInputHandlers } from 'hooks/useAddressInputHandlers';

import { useFullAppartment } from './AddressFullApartment.hooks';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullApartment = () => {
    const { handleInputSubmit, handleInputFocus } = useInputHandlers();

    const { handleInputChange, inputValue } = useFullAppartment();

    return (
        <StyledInput
            title="Квартира"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
            onInputFocus={handleInputFocus}
        />
    );
};
