import React from 'react';
import styled from 'styled-components';
import { background, gradient } from '@sberdevices/plasma-tokens';
import { Theme } from 'components/Theme/Theme';
import { TransparentPixel } from 'components/TransparentPixel/TransparentPixel';
import { preventScroll } from 'tools/scroll';

const StyledContainer = styled.div`
    position: relative;

    width: 100%;
    height: 100%;

    overflow: hidden;

    background-color: ${background};
    background-image: ${gradient};
`;

export const AppContentCommon = styled.div`
    position: relative;
    z-index: 2;
`;

interface AppLayoutProps {
    renderHeader(): React.ReactElement;
    renderContent(): React.ReactElement;
}
export const AppLayoutCommon: React.FC<AppLayoutProps> = ({ renderHeader, renderContent }) => {
    const header = React.useMemo(() => renderHeader(), [renderHeader]);
    const content = React.useMemo(() => renderContent(), [renderContent]);

    return (
        <StyledContainer onScroll={preventScroll}>
            <Theme />
            {header}
            <TransparentPixel />
            {content}
        </StyledContainer>
    );
};
