import React from 'react';
import {
    GuidePageHint,
    GuidePagePlasmaIconSberbox,
    GuidePageSourceSberbox,
    GuidePageTitleSberbox,
} from 'components/GuidePage/GuidePage@sberbox';
import {
    useKeyboardHintVisible,
    useShowKeyboardOnUnmout,
    useSwitchToInput,
} from 'pages/AddressSimpleGuide/AddressSimpleGuide.hooks';

import { AddressSimpleGuideCommon } from './AddressSimpleGuide';

export const AddressSimpleGuideSberbox: React.FC = () => {
    const isKeyboardHintVisible = useKeyboardHintVisible();
    const handleOkClick = useSwitchToInput();

    useShowKeyboardOnUnmout();

    return (
        <AddressSimpleGuideCommon>
            <GuidePagePlasmaIconSberbox />
            <GuidePageTitleSberbox>Нажмите кнопку на пульте и назовите</GuidePageTitleSberbox>
            <GuidePageSourceSberbox>улицу и дом</GuidePageSourceSberbox>
            {isKeyboardHintVisible && <GuidePageHint onOkClick={handleOkClick} />}
        </AddressSimpleGuideCommon>
    );
};
