import React from 'react';
import styled from 'styled-components';
import { HeaderPortal } from 'components/Header/Header@portal';
import { layout } from 'style/const/layout@portal';

import { AppLayoutCommon, AppContentCommon } from './AppLayout';

const AppContentPortal = styled(AppContentCommon)`
    height: ${layout.contentHeight}px;
`;

export const AppLayoutPortal: React.FC = ({ children }) => {
    const renderHeader = React.useCallback(() => <HeaderPortal />, []);
    const renderContent = React.useCallback(() => <AppContentPortal>{children}</AppContentPortal>, [children]);

    return <AppLayoutCommon renderHeader={renderHeader} renderContent={renderContent} />;
};
