import React from 'react';
import {
    DialogPagePortal,
    DialogTitlePortal,
    DialogButtonPortal,
    DialogTextPortal,
} from 'components/DialogPage/DialogPage@portal';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import { StatusEmptyDecoration } from './Decoration/StatusEmptyDecoration';
import { useStatusEmptyPage } from './StatusEmpty.hooks';

export const StatusEmptyPortal: React.FC = () => {
    const { handleCloseClick } = useStatusEmptyPage();

    const buttons = React.useMemo(
        () => [
            <DialogButtonPortal shouldFocusOnMount view={ButtonViews.secondary} onClick={handleCloseClick}>
                Не сейчас
            </DialogButtonPortal>,
        ],
        [handleCloseClick],
    );

    const decorations = <StatusEmptyDecoration />;

    return (
        <DialogPagePortal buttons={buttons} decorations={decorations}>
            <DialogTitlePortal>
                <MultilineText text="Заказов пока нет" />
            </DialogTitlePortal>
            <DialogTextPortal>
                <MultilineText text="Чтобы сделать заказ, скажите «Хочу попкорн»" />
            </DialogTextPortal>
        </DialogPagePortal>
    );
};
