import styled from 'styled-components';
import React from 'react';
import { Headline2 } from '@sberdevices/plasma-styles';

import {
    GuidePagePlasmaIconCommon,
    GuidePageTitleCommon,
    StyledCirclesCommon,
    StyledPlasmaIconCommon,
} from './GuidePage';
import { guidePageSourceCommonMixin } from './GuidePage.mixins';

const circleContainerSize = 256;
const plasmaIconSize = 64;
const offsetToCenteringIcon = circleContainerSize / 2 - plasmaIconSize / 2;
const StyledPlasmaIconPortal = styled(StyledPlasmaIconCommon)`
    position: absolute;
    height: ${plasmaIconSize}px;
    width: ${plasmaIconSize}px;
    top: ${offsetToCenteringIcon}px;
    left: ${offsetToCenteringIcon}px;
`;

const StyledCirclesPortal = styled(StyledCirclesCommon)`
    height: ${circleContainerSize}px;
    width: ${circleContainerSize}px;
    margin-top: 112px;

    &::after {
        top: 64px;
        left: 64px;
        right: 64px;
        bottom: 64px;
    }
`;

export const GuidePagePlasmaIconPortal = () => (
    <GuidePagePlasmaIconCommon sizePx={32}>
        <StyledCirclesPortal>
            <StyledPlasmaIconPortal />
        </StyledCirclesPortal>
    </GuidePagePlasmaIconCommon>
);

export const GuidePageTitlePortal = styled(GuidePageTitleCommon)`
    margin-top: -40px;
`;

export const GuidePageSourcePortal = styled(Headline2)`
    margin-top: 12px;

    ${guidePageSourceCommonMixin}
`;
