import { DeviceFamily, deviceFamily } from 'tools/environment';

import { RootPortal } from './Root@portal';
import { RootSberbox } from './Root@sberbox';
import { RootTouch } from './Root@touch';

const mapDeviceFamilyToRoot: Record<DeviceFamily, React.FC> = {
    [DeviceFamily.UNKNOWN]: RootSberbox,
    [DeviceFamily.SBERBOX]: RootSberbox,
    [DeviceFamily.PORTAL]: RootPortal,
    [DeviceFamily.MOBILE]: RootTouch,
};

export const Root = mapDeviceFamilyToRoot[deviceFamily];
