import React from 'react';
import styled, { css, keyframes } from 'styled-components';
import { Body2, Button2, Footnote1, Footnote2 } from '@sberdevices/plasma-styles';
import { accent, buttonSecondary, primary, surfaceLiquid01, warning } from '@sberdevices/plasma-tokens';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addItemAction } from 'store/actions/native';
import { Badge, BadgeProps } from 'components/Badge/Badge';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { icon } from 'style/const/icon';
import { transition } from 'style/const/transition';
import { ReactComponent as PlusSvg } from 'assets/icons/plus.svg';
import { ReactComponent as DoneSvg } from 'assets/icons/done.svg';
import { itemDetailsItemSelectThunk } from 'store/thunks/itemDetailsPage';

export const cardWidthPx = 330;
export const cardHeightPx = 506;
export const cardBorderRadiusPx = 40;

const thumbSizePx = cardWidthPx;

interface StyledThumbProps {
    imageUrl: string;
}
const StyledThumb = styled.div<StyledThumbProps>`
    display: block;
    height: ${thumbSizePx}px;
    width: ${thumbSizePx}px;

    background: rgba(255, 255, 255, 0.04);
    background-image: url('${(props) => props.imageUrl}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    border-top-left-radius: ${cardBorderRadiusPx}px;
    border-top-right-radius: ${cardBorderRadiusPx}px;
`;

const descriptionHeightPx = cardHeightPx - thumbSizePx;
const descriptionPaddingPx = 32;

const StyledDescription = styled.div`
    position: relative;

    height: ${descriptionHeightPx}px;

    box-sizing: border-box;
    padding: ${descriptionPaddingPx}px;

    border-bottom-left-radius: ${cardBorderRadiusPx}px;
    border-bottom-right-radius: ${cardBorderRadiusPx}px;
`;

const nameLineCount = 2;

const StyledName = styled(Footnote1)`
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: ${nameLineCount};
    overflow: hidden;

    color: ${primary};
`;

const StyledPriceContainer = styled.div`
    position: absolute;
    bottom: 36px;

    display: flex;
`;

const StyledPrice = styled(Footnote2)`
    color: ${primary};
`;

const StyledOldPrice = styled(Footnote2)`
    position: relative;
    opacity: 0.24;
    color: ${primary};
    margin-left: 8px;

    &::before {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        border-top: 4px solid ${primary};
        left: 2px;
        right: 2px;
    }
`;

interface StyledCountProps {
    isWarning?: boolean;
}
const StyledCount = styled(Body2)<StyledCountProps>`
    margin-left: 8px;

    color: ${({ isWarning }) => (isWarning ? warning : accent)};
`;

const buttonIconSizePx = icon.m.sizePx;
const buttonSizePx = 72;
const buttonOffsetXPx = 12;
const buttonOffsetYPx = -52;

interface StyledButtonProps {
    isDisabled?: boolean;
}
const StyledButton = styled(Button2)<StyledButtonProps>`
    position: absolute;
    top: ${buttonOffsetYPx}px;
    right: ${buttonOffsetXPx}px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: ${buttonSizePx}px;
    height: ${buttonSizePx}px;

    border-radius: 50%;

    background-color: ${({ isDisabled }) => (isDisabled ? buttonSecondary : accent)};

    opacity: 0;

    transition: opacity ${transition.default}, background-color ${transition.default};
`;

const StyledPlusIcon = styled(PlusSvg)`
    width: ${buttonIconSizePx}px;
    height: ${buttonIconSizePx}px;
`;

const StyledDoneIcon = styled(DoneSvg)`
    width: ${buttonIconSizePx}px;
    height: ${buttonIconSizePx}px;
`;

const badgePositionPx = 16;

const CatalogListItemBadge = styled(Badge)`
    position: absolute;
    top: ${badgePositionPx}px;
    left: ${badgePositionPx}px;
    z-index: 1;
`;

const StyledIndexBadge = styled(IndexBadge)`
    top: 8px;
    right: 8px;
`;

const cardScaleFactor = 1 / 1.04;
const addActiveDurationMs = 200;

const AddAnimation = css`
    animation-name: ${keyframes`
        0% {
            transform: scale(1);
        }
        100% {
            transform: scale(${cardScaleFactor});
        }
    `};
    animation-duration: ${addActiveDurationMs / 2}ms;
    animation-iteration-count: 2;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-direction: alternate;
`;

const shakeDurationMs = 200;
const shakeAmplitudePx = 10;

const ShakeAnimation = css`
    animation-name: ${keyframes`
        0% {
            transform: translateX(0);
        }
        25% {
            transform: translateX(${-1 * shakeAmplitudePx}px);
        }
        75% {
            transform: translateX(${shakeAmplitudePx}px);
        }
        100% {
            transform: translateX(0);
        }
    `};
    animation-duration: ${shakeDurationMs}ms;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
`;

interface StyledCatalogListItemProps {
    disabled?: boolean;
    isActive: boolean;
    isButtonDisabled?: boolean;
    isShaking: boolean;
}
const StyledCatalogListItem = styled.div<StyledCatalogListItemProps>`
    position: relative;
    width: ${cardWidthPx}px;
    height: ${cardHeightPx}px;

    display: flex;
    flex-direction: column;
    grid-row: 1;

    border-radius: ${cardBorderRadiusPx}px;
    box-shadow: 0 16px 48px rgba(0, 0, 0, 0.1);
    background: ${surfaceLiquid01};

    transition: ${addActiveDurationMs / 2}ms ${transition.timingFn.default};
    will-change: transform;
    will-change: background-color;

    ${({ disabled, isButtonDisabled }) =>
        !disabled &&
        css`
            ${StyledButton} {
                opacity: ${isButtonDisabled ? '0.56' : '1'};
            }
        `}

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledDescription},
            ${StyledThumb} {
                opacity: 0.5;
            }
        `}

    ${({ isActive }) =>
        isActive &&
        css`
            ${AddAnimation}
        `}

    ${({ isShaking }) =>
        isShaking &&
        css`
            ${ShakeAnimation}
        `}
`;

const useActive = (count: number) => {
    const [isCardActive, setCardActive] = React.useState(false);
    const [isButtonActive, setButtonActive] = React.useState(false);
    const [displayCount, setDisplayCount] = React.useState(count);
    const prevCountRef = React.useRef(0);

    // проставление стейта нужно делать обязательно в эффекте,
    // иначе происходит слишком много перерисовок из-за того что,
    // мы во время рендера вызываем проставление стейта и так по кругу
    // реакту такое не нравится
    React.useEffect(() => {
        if (count > prevCountRef.current) {
            const nextCount = count;

            setCardActive(true);

            setTimeout(() => {
                setButtonActive(true);
                setDisplayCount(nextCount);
            }, addActiveDurationMs * 0.5);

            setTimeout(() => {
                setCardActive(false);
            }, addActiveDurationMs);

            setTimeout(() => {
                setButtonActive(false);
            }, addActiveDurationMs * 1.5);
        } else {
            setDisplayCount(count);
        }

        prevCountRef.current = count;
    }, [count]);

    return {
        isCardActive,
        isButtonActive,
        displayCount,
    };
};

const useShake = () => {
    const [isShaking, setShaking] = React.useState(false);

    const doShake = React.useCallback(() => {
        if (!isShaking) {
            setShaking(true);
        }

        setTimeout(() => setShaking(false), shakeDurationMs);
    }, [isShaking, setShaking]);

    return { isShaking, doShake };
};

export interface CatalogListItemProps {
    id: string;
}

export interface CatalogListItemViewProps {
    imageUrl: string;
    count: number;
    isWantMoreThanHave?: boolean;
    name: string;
    index: number;
    price: number;
    oldPrice?: number;
    badge?: BadgeProps;
    disabled?: boolean;
    isButtonDisabled?: boolean;
}

export const CatalogListItemPortal: React.FC<CatalogListItemProps> = ({ id }) => {
    const dispatch = useDispatch();
    const {
        imageUrl,
        count,
        isWantMoreThanHave,
        index,
        name,
        price,
        oldPrice,
        badge,
        disabled,
        isButtonDisabled,
    } = useSelector(({ catalogPagePortal }) => catalogPagePortal.itemsMap[id]);
    const { isCardActive, isButtonActive, displayCount } = useActive(count);
    const { isShaking, doShake } = useShake();

    const handleAdd = React.useCallback(
        (e: React.MouseEvent) => {
            e.stopPropagation();

            if (disabled) {
                return;
            }

            if (isButtonDisabled) {
                doShake();
            } else {
                dispatch(addItemAction({ item: id, count: '1', skipFocus: true }));
            }
        },
        [dispatch, id, disabled, isButtonDisabled, doShake],
    );

    const handleClick = React.useCallback(() => {
        dispatch(itemDetailsItemSelectThunk(id));
    }, [dispatch, id]);

    return (
        <StyledCatalogListItem
            tabIndex={0}
            disabled={disabled}
            isButtonDisabled={isButtonDisabled}
            isActive={isCardActive}
            isShaking={isShaking}
            onClick={handleClick}
        >
            {badge ? <CatalogListItemBadge {...badge} /> : null}
            <StyledIndexBadge size="m" index={index} />
            <StyledThumb imageUrl={imageUrl} />
            <StyledDescription>
                <StyledButton isDisabled={isButtonDisabled} onClick={handleAdd}>
                    {isButtonActive ? <StyledDoneIcon /> : <StyledPlusIcon />}
                </StyledButton>
                <StyledName>{name}</StyledName>
                <StyledPriceContainer>
                    {price ? <StyledPrice>{price} ₽</StyledPrice> : null}
                    {oldPrice ? <StyledOldPrice>{oldPrice} ₽</StyledOldPrice> : null}
                    {displayCount ? <StyledCount isWarning={isWantMoreThanHave}>×{displayCount}</StyledCount> : null}
                </StyledPriceContainer>
            </StyledDescription>
        </StyledCatalogListItem>
    );
};
