import React from 'react';
import styled from 'styled-components';
import { Label } from 'components/Label/Label';
import { Input } from 'components/Input/Input';

import { useFullPhone } from './AddressFullPhone.hooks';

const StyledContainer = styled.div`
    position: relative;

    margin: 80px auto 0;

    width: 900px;
`;

const StyledLabel = styled(Label)`
    position: absolute;

    margin-top: 12px;

    width: 100%;
    padding: 0 32px;
`;

export const AddressFullPhone = () => {
    const { handleInputChange, inputValue, isErrorLabelVisible, handleInputFocus, handleInputSubmit } = useFullPhone();

    return (
        <StyledContainer>
            <Input
                title="Номер телефона"
                value={inputValue}
                shouldFocusInputOnMount
                onChange={handleInputChange}
                onSubmit={handleInputSubmit}
                onInputFocus={handleInputFocus}
            />
            {isErrorLabelVisible ? <StyledLabel type="critical" content="Неверно введен номер" /> : null}
        </StyledContainer>
    );
};
