import React from 'react';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useInputHandlers } from 'hooks/useAddressInputHandlers';

import { useFullDoorCode } from './AddressFullDoorCode.hooks';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullDoorCode = () => {
    const { handleInputFocus, handleInputSubmit } = useInputHandlers();

    const { inputValue, handleInputChange } = useFullDoorCode();

    return (
        <StyledInput
            title="Комментарий"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
            onInputFocus={handleInputFocus}
        />
    );
};
