import React from 'react';
import { push } from 'connected-react-router';
import styled from 'styled-components';
import { Underline } from '@sberdevices/plasma-styles';
import { buttonAccent, primary } from '@sberdevices/plasma-tokens';
import { useDispatch } from 'hooks/useDispatch';
import { ROUTES } from 'store/constants/router';
import { HeaderButtonTouch } from 'components/HeaderButton/HeaderButton@touch';
import { useHeaderCart, useHeaderVisibilityFlags } from 'components/Header/Header.hooks';
import {
    routeToVisibilityMapCommon,
    routesToAddressVisibilityMapCommon,
    routesToCartVisibilityMapCommon,
} from 'components/Header/Header';
import { icon } from 'style/const/icon';

import { ReactComponent as CartIcon } from './HeaderCart.assets/cart.svg';

const cartIconSizePx = icon.xs.sizePx;

const StyledCart = styled.div`
    position: relative;
`;

const StyledIcon = styled(CartIcon)`
    width: ${cartIconSizePx}px;
    height: ${cartIconSizePx}px;
`;

const badgeSizePx = 16;
const badgeOffsetPx = cartIconSizePx / 2;

const StyledBadge = styled(Underline)`
    position: absolute;
    left: ${badgeOffsetPx}px;
    bottom: ${badgeOffsetPx}px;

    width: ${badgeSizePx}px;
    height: ${badgeSizePx}px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;

    color: ${primary};
    background: ${buttonAccent};
`;

export const HeaderCartTouch: React.FC = () => {
    const dispatch = useDispatch();

    const { count, price } = useHeaderCart();
    const { isCartVisible: isVisible } = useHeaderVisibilityFlags({
        routeToVisibilityMap: routeToVisibilityMapCommon,
        routesToAddressVisibilityMap: routesToAddressVisibilityMapCommon,
        routesToCartVisibilityMap: routesToCartVisibilityMapCommon,
    });

    const handleClick = React.useCallback(() => {
        dispatch(push(ROUTES.CART));
    }, [dispatch]);

    const text = `${price} ₽`;

    return (
        <HeaderButtonTouch isVisible={isVisible} onClick={handleClick} text={text}>
            <StyledCart>
                <StyledIcon />
                <StyledBadge>{count}</StyledBadge>
            </StyledCart>
        </HeaderButtonTouch>
    );
};
