import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useAddressSimpleUpdateSuggests } from 'pages/AddressSimple/AddressSimple.hooks';
import { Button } from 'components/Button/Button';
import { GradientFade } from 'components/GradientFade/GradientFade';

export const StyledSwiperContainerCommon = styled.div`
    overflow-y: hidden;
`;

export const StyledSwiperWrapper = styled.div``;

export const StyledButtonCommon = styled(Button)`
    width: 100%;
`;

interface AddressSimpleProps {
    renderTitle(): React.ReactElement;
    renderSuggestItems(): React.ReactElement[];
    renderSwiperContainer(children: React.ReactElement[]): React.ReactElement;
}

export const AddressSimpleCommon: React.FC<AddressSimpleProps> = ({
    renderTitle,
    renderSuggestItems,
    renderSwiperContainer,
}) => {
    const suggestsListIndex = useSelector(({ addressSimplePage }) => addressSimplePage.suggestsListIndex);

    useAddressSimpleUpdateSuggests();

    const title = React.useMemo(() => renderTitle(), [renderTitle]);
    const suggestItems = React.useMemo(() => renderSuggestItems(), [renderSuggestItems]);

    const isTopGradientVisible = suggestsListIndex > 1;
    const isBottomGradientVisible =
        // prettier-ignore
        (suggestsListIndex === 0 && suggestItems.length > 1) ||
        // -2 потому что все элементы уже видны, когда мы доходим до этого индекса
        // пора убирать градиент снизу
        suggestsListIndex < suggestItems.length - 1 - 2;

    const swiperContainer = React.useMemo(() => renderSwiperContainer(suggestItems), [
        renderSwiperContainer,
        suggestItems,
    ]);

    return (
        <>
            {title}
            <GradientFade sizePx={150} isVisible={isTopGradientVisible} kind="top">
                <GradientFade sizePx={250} isVisible={isBottomGradientVisible} kind="bottom">
                    {swiperContainer}
                </GradientFade>
            </GradientFade>
        </>
    );
};
