import { createGlobalStyle, css } from 'styled-components';
import { Layout } from 'style/const/layout';

const layoutVarPrefix = '--layout';

export const layoutWidthVar = `${layoutVarPrefix}-width`;
export const layoutHeightVar = `${layoutVarPrefix}-height`;
export const layoutHeaderHeightVar = `${layoutVarPrefix}-header-height`;
export const layoutContentHeightVar = `${layoutVarPrefix}-content-height`;
export const layoutFooterHeightVar = `${layoutVarPrefix}-footer-height`;
export const layoutPaddingSide = `${layoutVarPrefix}-padding-side`;

interface GlobalLayoutVarsProps {
    layout: Layout;
}
export const GlobalLayoutVars = createGlobalStyle<GlobalLayoutVarsProps>`
    :root {
        ${({ layout }) => css`
            ${layoutWidthVar}: ${layout.width}px;
            ${layoutHeightVar}: ${layout.height}px;
            ${layoutHeaderHeightVar}: ${layout.headerHeight}px;
            ${layoutContentHeightVar}: ${layout.contentHeight}px;
            ${layoutFooterHeightVar}: ${layout.footerHeight}px;
            ${layoutPaddingSide}: ${layout.paddingSide}px;
        `}
    }
`;
