import { AppReducer } from 'store/constants';

export const categoriesModel: AppReducer = (state, action) => {
    const { categoriesData, categoriesPage } = state;

    switch (action.type) {
        case 'SHOW_CATEGORIES': {
            const [firstItem] = Object.values(categoriesData.itemsMap);

            categoriesPage.activeItemId = firstItem ? firstItem.id : '';
            break;
        }
        case 'categories/SWITCH_ACTIVE_ITEM': {
            categoriesPage.activeItemId = action.payload;
            break;
        }
        default:
            break;
    }
};
