import React from 'react';
import styled from 'styled-components';
import { Headline2 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import { useAddressSimpleUpdateSuggests, useSwiper } from 'pages/AddressSimple/AddressSimple.hooks';
import { ButtonSize, ButtonViews } from 'components/Button/Button';
import { preventScroll } from 'tools/scroll';

import {
    AddressSimpleCommon,
    StyledButtonCommon,
    StyledSwiperContainerCommon,
    StyledSwiperWrapper,
} from './AddressSimple';
import { useHandleCantFindAddressClick } from './AddressSimple.hooks';
import { selectIsAddressSubmitting, selectSuggestIds, selectSuggestsListIndex } from './AddressSimple.selectors';
import { AddressSimpleSuggestListItemPortal } from './SuggestItem/AddressSimpleSuggestItem@portal';

const slideHeight = 80;
const spaceBetween = 24;
const slideWithMarginHeight = slideHeight + spaceBetween;
const paddingForFocus = 8;
const itemsInViewport = 4;

const StyledSwiperContainer = styled(StyledSwiperContainerCommon)`
    margin: 56px auto 0;
    width: 800px;
    height: ${slideWithMarginHeight * itemsInViewport - spaceBetween + paddingForFocus * 2 + slideHeight / 2}px;
`;

const StyledSwiperSlide = styled.div`
    height: ${slideHeight}px;
`;

const StyledTitle = styled(Headline2)`
    margin-top: 48px;
    color: ${primary};
    text-align: center;
`;

const StyledButton = styled(StyledButtonCommon)`
    padding: 24px 0;
`;

export const AddressSimplePortal: React.FC = () => {
    const isAddressSubmitting = useSelector(selectIsAddressSubmitting);
    const suggestIds = useSelector(selectSuggestIds);
    const suggestsListIndex = useSelector(selectSuggestsListIndex);
    const handleCantFindAddressClick = useHandleCantFindAddressClick();

    useAddressSimpleUpdateSuggests();

    const swiperContainerRef = useSwiper({ paddingForFocus, spaceBetween, suggestIds, suggestsListIndex });

    const renderTitle = React.useCallback(() => <StyledTitle>Выберите адрес</StyledTitle>, []);

    const renderSuggestItems = React.useCallback(
        () =>
            suggestIds
                .map((itemId, index) => (
                    <StyledSwiperSlide className="swiper-slide" key={itemId}>
                        <AddressSimpleSuggestListItemPortal shouldFocusOnMount={index === 0} id={itemId} />
                    </StyledSwiperSlide>
                ))
                .concat([
                    <StyledSwiperSlide className="swiper-slide" key="button">
                        <StyledButton
                            view={ButtonViews.primary}
                            size={ButtonSize.s}
                            onClick={handleCantFindAddressClick}
                            disabled={isAddressSubmitting}
                        >
                            Моего адреса нет в списке
                        </StyledButton>
                    </StyledSwiperSlide>,
                ]),
        [suggestIds, handleCantFindAddressClick, isAddressSubmitting],
    );

    const renderSwiperContainer = React.useCallback(
        (children: React.ReactElement[]) => (
            <StyledSwiperContainer
                tabIndex={-1}
                className="swiper-container"
                ref={swiperContainerRef}
                onScroll={preventScroll}
            >
                <StyledSwiperWrapper className="swiper-wrapper">{children}</StyledSwiperWrapper>
            </StyledSwiperContainer>
        ),
        [swiperContainerRef],
    );

    return (
        <AddressSimpleCommon
            renderSuggestItems={renderSuggestItems}
            renderSwiperContainer={renderSwiperContainer}
            renderTitle={renderTitle}
        />
    );
};
