import { DefaultImportedComponent, lazy } from '@loadable/component';
import pMinDelay from 'p-min-delay';
import { timeout as promiseTimeout } from 'promise-timeout';

interface ImportOptions {
    delay?: number;
    timeout?: number;
}

const DEFAULT_DELAY = 200;
const DEFAULT_TIMEOUT = 10000;

export const lazyImport = <T>(
    importedModule: Promise<DefaultImportedComponent<T>>,
    { delay = DEFAULT_DELAY, timeout = DEFAULT_TIMEOUT }: ImportOptions = {},
) => lazy(() => promiseTimeout(pMinDelay(importedModule, delay), timeout));
