import { SberVisorAnalytics, SberVisorPushParams, SberVisorPushProps } from '@sberdevices/analytics/lib/sbervisor';
import amplitude, { ExtendedAmplitudeClient } from 'amplitude-js';
import { deviceFamily, isPROMStand } from 'tools/environment';

const sberVisorApiKey = '6a619dbfc07f390933233c530907f92fc89607711eed7508d4d59cb8636b674a';
const sberVisorEndpoint = 'https://sa.online.sberbank.ru:8098/metrics/partners';

const amplitudeApiKey = isPROMStand ? '7708ad44aba64d66d6f0f7aa06da4378' : '8a7afa2bca611718d7ed11baaea3d14d';

type AnalyticsPushParams = SberVisorPushParams;

interface AnalyticsOptions {
    tags?: string[];
    properties?: SberVisorPushProps;
}

interface AnalyticsEngine {
    type: string;
    setUserId(userId: string): void;
    setDeviceId(deviceId: string): void;
    push(params: AnalyticsPushParams): void;
}

class SberAnalytics implements AnalyticsEngine {
    public type = 'sber';

    private userId = '';

    private deviceId = '';

    private instance = new SberVisorAnalytics({
        url: sberVisorEndpoint,
        apiKey: sberVisorApiKey,
        buffer: 1,
    });

    public setUserId(userId: string): void {
        this.userId = userId;
    }

    public setDeviceId(deviceId: string): void {
        this.deviceId = deviceId;
    }

    public push(params: AnalyticsPushParams) {
        this.instance.sendEvent(() => ({
            ...params,
            properties: { ...params.properties, userId: this.userId, deviceId: this.deviceId },
        }));
    }
}

class AmpAnalytics implements AnalyticsEngine {
    public type = 'amp';

    private instance: ExtendedAmplitudeClient = amplitude.getInstance();

    public constructor() {
        this.instance.init(amplitudeApiKey);
        // eslint-disable-next-line no-underscore-dangle
        this.instance._ua = {
            browser: {
                name: null,
                major: null,
            },
            os: {
                name: deviceFamily,
            },
        };
    }

    public setUserId(userId: string): void {
        this.instance.setUserId(userId);
    }

    public setDeviceId(deviceId: string): void {
        this.instance.setDeviceId(deviceId);
    }

    public push(params: AnalyticsPushParams) {
        const { eventAction: event, ...data } = params;
        this.instance.logEvent(event, data);
    }
}

function createAnalyticsEngine(): AnalyticsEngine[] {
    const amp = new AmpAnalytics();
    const sber = new SberAnalytics();
    return [amp, sber];
}

class Analytics {
    private analytics: AnalyticsEngine[];

    private deviceId = '';

    public constructor(analytics: AnalyticsEngine[]) {
        this.analytics = analytics;

        const userId = (Date.now() + Math.floor(Math.random() * 1000)).toString();

        this.setUserId(userId);

        // eslint-disable-next-line no-console
        console.log('userId', userId);

        this.push({
            eventAction: 'Cтарт',
            eventCategory: 'samokat',
            eventType: 'init',
            value: 'start',
        });
    }

    public getDeviceId() {
        return this.deviceId;
    }

    public setUserId(userId: string) {
        this.analytics.forEach((a) => a.setUserId(userId));
    }

    public setDeviceId(deviceId: string) {
        this.analytics.forEach((a) => a.setDeviceId(deviceId));
        this.deviceId = deviceId;
    }

    public push(params: AnalyticsPushParams) {
        this.analytics.forEach((a) => a.push(params));
    }

    public remoteNavigate(label: string, opts?: AnalyticsOptions) {
        this.push({
            eventAction: label,
            eventCategory: 'samokat',
            eventType: 'navigate',
            value: label,
            properties: opts?.properties,
            tags: opts?.tags,
        });
    }

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    public redux(label: string, action: any, opts?: AnalyticsOptions) {
        const { payload = {} } = action;

        this.push({
            eventAction: label,
            eventCategory: 'samokat',
            eventType: 'redux',
            value: opts?.toString() || '',
            properties: { payload, ...opts?.properties },
            tags: opts?.tags,
        });
    }
}

export const analyticsInstance = new Analytics(createAnalyticsEngine());
