import { AppState, AppReducer } from 'store/constants';
import { selectProductById } from 'store/selectors';
import { calculateButtonDisabled, formatPrice, prepareBadge, prepareCount } from 'store/reducers/helpers/catalogPage';

const prepareIndex = ({ catalogPageTouch: { order } }: AppState, itemId: string) => order.indexOf(itemId) + 1;

const prepareCatalogItemsMap = (state: AppState) => {
    const { productsData } = state;

    return Object.keys(productsData.itemsMap).reduce<AppState['catalogPageTouch']['itemsMap']>((acc, itemId) => {
        const productItem = selectProductById(state, itemId);

        acc[itemId] = {
            imageUrl: productItem.imageUrl,
            name: productItem.name,
            price: formatPrice(productItem.price),
            count: prepareCount(state, itemId),
            index: prepareIndex(state, itemId),
            badge: prepareBadge(state, itemId)?.type,
            buttonDisabled: calculateButtonDisabled(state, itemId),
            disabled: productItem.quantityLeft === 0,
        };

        return acc;
    }, {});
};

export const catalogPageTouchView: AppReducer = (state) => {
    const { productsData, catalogPageTouch } = state;

    catalogPageTouch.order = Object.keys(productsData.itemsMap);
    catalogPageTouch.itemsMap = prepareCatalogItemsMap(state);
};
