import React from 'react';
import { ButtonSize, ButtonViews } from 'components/Button/Button';
import {
    LocationButtonPortal,
    LocationMapBackgroundPortal,
    LocationPagePortal,
    LocationTextPortal,
    LocationTitlePortal,
} from 'components/LocationPage/LocationPage@portal';

import { AddressFoundPin, TryAgainPin } from './AddressSimpleQuestion';
import { useAddressFound, useAddressSimpleQuestion, useTryAgain } from './AddressSimpleQuestion.hooks';

const TryAgainPortal: React.FC = () => {
    const { inputValue, handleRepeatClick } = useTryAgain();

    const buttons = React.useMemo(
        () => (
            <LocationButtonPortal
                shouldFocusOnMount
                size={ButtonSize.s}
                view={ButtonViews.secondary}
                onClick={handleRepeatClick}
            >
                Ввести еще раз
            </LocationButtonPortal>
        ),
        [handleRepeatClick],
    );

    const background = React.useMemo(() => <LocationMapBackgroundPortal />, []);

    return (
        <LocationPagePortal buttons={buttons} background={background}>
            <TryAgainPin />
            <LocationTitlePortal>{inputValue}</LocationTitlePortal>
            <LocationTextPortal>Этот адрес не найден, попробуйте еще раз</LocationTextPortal>
        </LocationPagePortal>
    );
};

const AddressFoundPortal: React.FC = () => {
    const { addressName, isAddressSubmitting, handleAcceptButtonClick, handleDeclineButtonClick } = useAddressFound();

    const background = React.useMemo(() => <LocationMapBackgroundPortal />, []);

    const buttons = React.useMemo(
        () => (
            <>
                <LocationButtonPortal
                    shouldFocusOnMount
                    shouldLockAfterClick
                    size={ButtonSize.s}
                    view={ButtonViews.primary}
                    onClick={handleAcceptButtonClick}
                >
                    Да, все верно
                </LocationButtonPortal>
                <LocationButtonPortal
                    size={ButtonSize.s}
                    view={ButtonViews.secondary}
                    onClick={handleDeclineButtonClick}
                    disabled={isAddressSubmitting}
                >
                    Нет, выбрать другой
                </LocationButtonPortal>
            </>
        ),
        [handleAcceptButtonClick, handleDeclineButtonClick, isAddressSubmitting],
    );

    return (
        <LocationPagePortal buttons={buttons} background={background}>
            <AddressFoundPin />
            <LocationTitlePortal>{addressName}</LocationTitlePortal>
            <LocationTextPortal>Это ваш адрес?</LocationTextPortal>
        </LocationPagePortal>
    );
};

export const AddressSimpleQuestionPortal: React.FC = () => {
    const { isSuggestFetchPenging, suggest } = useAddressSimpleQuestion();

    if (isSuggestFetchPenging) {
        return <LocationPagePortal />;
    }

    if (!suggest) {
        return <TryAgainPortal />;
    }

    return <AddressFoundPortal />;
};
