import React from 'react';
import { useSelector } from 'hooks/useSelector';
import {
    CartInfoPinPortal,
    CartInfoContainerPortal,
    CartPersonalPortal,
    CartAddressPortal,
} from 'components/Cart/Info/CartInfo@portal';

export const StatusDetailsInfoPortal: React.FC = () => {
    const { address } = useSelector(({ userData }) => userData);
    const { phoneNumber, personalAddress } = useSelector(({ orderStatusDetailsPage }) => orderStatusDetailsPage);

    return (
        <CartInfoContainerPortal>
            <CartInfoPinPortal />
            <CartAddressPortal>{address}</CartAddressPortal>
            <CartPersonalPortal address={personalAddress} phone={phoneNumber} />
        </CartInfoContainerPortal>
    );
};
