import { AppReducer } from 'store/constants';
import { isCharacter } from 'typeguards/isCharacter';

export const characterData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'character/SET_CHARACTER': {
            if (isCharacter(action.payload)) {
                state.character.id = action.payload;
            }

            break;
        }

        default:
            break;
    }
};
