import React from 'react';
import { push } from 'connected-react-router';
import styled from 'styled-components';
import { Underline } from '@sberdevices/plasma-styles';
import { buttonAccent, primary } from '@sberdevices/plasma-tokens';
import { useDispatch } from 'hooks/useDispatch';
import { ROUTES } from 'store/constants/router';
import { HeaderButton } from 'components/HeaderButton/HeaderButton';
import { icon } from 'style/const/icon';

import { useHeaderCart } from '../Header/Header.hooks';

import { ReactComponent as CartSvg } from './HeaderCart.assets/cart.svg';

const cartIconSizePx = icon.m.sizePx;

const StyledCart = styled.div`
    position: relative;
`;

const StyledIcon = styled(CartSvg)`
    width: ${cartIconSizePx}px;
    height: ${cartIconSizePx}px;
`;

const badgeSizePx = 32;
const badgeOffsetPx = cartIconSizePx / 2;

const StyledBadge = styled(Underline)`
    position: absolute;
    left: ${badgeOffsetPx}px;
    bottom: ${badgeOffsetPx}px;

    width: ${badgeSizePx}px;
    height: ${badgeSizePx}px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;

    color: ${primary};
    background: ${buttonAccent};
`;

interface HeaderCartProps {
    forwardRef?: React.RefObject<HTMLButtonElement>;
    isVisible: boolean;
}

export const HeaderCart: React.FC<HeaderCartProps> = (props) => {
    const dispatch = useDispatch();

    const { count, price } = useHeaderCart();

    const handleClick = React.useCallback(() => {
        dispatch(push(ROUTES.CART));
    }, [dispatch]);

    const text = `${price} ₽`;

    return (
        <HeaderButton onClick={handleClick} text={text} {...props}>
            <StyledCart>
                <StyledIcon />
                <StyledBadge>{count}</StyledBadge>
            </StyledCart>
        </HeaderButton>
    );
};
