import React from 'react';
import styled from 'styled-components';
import { useContainerFocus } from 'hooks/useContainerFocus';
import { layoutHeaderHeightVar, layoutPaddingSide } from 'components/GlobalLayoutVars/GlobalLayoutVars';
import { HeaderAddress } from 'components/HeaderAddress/HeaderAddress';
import { HeaderCart } from 'components/HeaderCart/HeaderCart';
import { transition } from 'style/const/transition';
import { ROUTES } from 'store/constants/router';

import { HeaderTitle } from './Title/HeaderTitle';
import { useHeaderVisibilityFlags } from './Header.hooks';

interface StyledHeaderProps {
    isVisible: boolean;
}

export const StyledHeader = styled.div<StyledHeaderProps>`
    --spatial-navigation-contain: contain;

    position: relative;
    z-index: 3;

    height: var(${layoutHeaderHeightVar});
    padding: 52px 88px 0 var(${layoutPaddingSide});

    box-sizing: border-box;

    display: flex;
    justify-content: space-between;
    align-items: center;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

export const routeToVisibilityMapCommon: Record<string, boolean> = {
    [ROUTES.CATALOG]: true,
    [ROUTES.CART]: true,
    [ROUTES.CATEGORIES]: true,
    [ROUTES.SUCCESS]: true,
    [ROUTES.STATUS_DETAILS]: true,
    [ROUTES.STATUS_SUMMARY]: true,
    [ROUTES.STATUS_REPORT]: true,
    [ROUTES.ERROR]: true,
    [ROUTES.NO_AREA]: true,
    [ROUTES.ADDRESS_FULL_ALL]: true,
    [ROUTES.ADDRESS_SIMPLE_QUESTION]: true,
    [ROUTES.ADDRESS_SIMPLE_GUIDE]: true,
    [ROUTES.ADDRESS_SIMPLE]: true,
    [ROUTES.ADDRESS_MAP]: true,
};

export const routesToAddressVisibilityMapCommon: Record<string, boolean> = {
    [ROUTES.CATALOG]: true,
};

export const routesToCartVisibilityMapCommon: Record<string, boolean> = {
    [ROUTES.CATALOG]: true,
};

const cartButtonRef = React.createRef<HTMLButtonElement>();

interface HeaderCommonProps {
    renderBackButton?: () => React.ReactNode;
    routesToCartVisibilityMap?: Record<string, boolean>;
    routesToAddressVisibilityMap?: Record<string, boolean>;
    routeToVisibilityMap?: Record<string, boolean>;
}

export const HeaderCommon: React.FC<HeaderCommonProps> = ({
    renderBackButton,
    routesToCartVisibilityMap = routesToCartVisibilityMapCommon,
    routesToAddressVisibilityMap = routesToAddressVisibilityMapCommon,
    routeToVisibilityMap = routeToVisibilityMapCommon,
    ...props
}) => {
    const { isHeaderVisible, isAddressVisible, isCartVisible } = useHeaderVisibilityFlags({
        routeToVisibilityMap,
        routesToAddressVisibilityMap,
        routesToCartVisibilityMap,
    });
    const handleReceiveFocus = React.useCallback(() => {
        if (isCartVisible) {
            cartButtonRef.current?.focus();
        }
    }, [isCartVisible]);

    const { handleFocus, handleBlur } = useContainerFocus({
        onReceiveFocus: handleReceiveFocus,
    });

    return (
        <StyledHeader isVisible={isHeaderVisible} onFocus={handleFocus} onBlur={handleBlur} {...props}>
            {renderBackButton?.()}
            <StyledSection>
                <HeaderTitle />
            </StyledSection>
            <StyledSection>
                <HeaderAddress isVisible={isAddressVisible} />
                <HeaderCart forwardRef={cartButtonRef} isVisible={isCartVisible} />
            </StyledSection>
        </StyledHeader>
    );
};
