import React from 'react';
import styled from 'styled-components';
import { noop } from 'tools/noop';
import { Button, ButtonViews } from 'components/Button/Button';
import { Input } from 'components/Input/Input';
import { ROUTES } from 'store/constants/router';

import { useAddressFull } from './AddressFullAll.hooks';
import {
    StyledFirstRow,
    StyledSecondRow,
    StyledThirdRow,
    StyledFourthRow,
    StyledPlasmaIcon,
    StyledVoiceHint,
} from './AddressFullAll@common';

const SubmitButton = styled(Button)`
    flex-grow: 0;
    padding: 24px 40px;

    &[disabled] {
        opacity: 0.3;
    }
`;

const StyledRoot = styled.div`
    display: grid;
    padding: 36px 112px 0;
    box-sizing: border-box;
    grid-gap: 32px 32px;
    grid-template-areas: 'first-row' 'second-row' 'third-row' 'fourth-row';

    will-change: transform;
`;

export const AddressFullAllPortal: React.FC = () => {
    const {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
        nextFocus,
        isPhoneValid,
        handleAddressKeyPress,
        handlePhoneKeyPress,
        handleApartmentKeyPress,
        handleSubmitClick,
        handleDoorCodeKeyPress,
        handleEntranceKeyPress,
        handleFloorKeyPress,
    } = useAddressFull();

    return (
        <StyledRoot>
            <StyledFirstRow>
                <Input
                    title="Улица и дом"
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleAddressKeyPress}
                    value={addressInputValue}
                />
                <Input
                    title="Номер телефона"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_PHONE}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handlePhoneKeyPress}
                    value={phoneNumberInputValue}
                    isInvalid={!isPhoneValid}
                />
            </StyledFirstRow>
            <StyledSecondRow>
                <Input
                    title="Квартира"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_APARTMENT}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleApartmentKeyPress}
                    value={apartmentInputValue}
                />
                <Input
                    title="Подъезд"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_ENTRANCE}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleEntranceKeyPress}
                    value={entranceInputValue}
                />
                <Input
                    title="Этаж"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_FLOOR}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleFloorKeyPress}
                    value={floorInputValue}
                />
            </StyledSecondRow>
            <StyledThirdRow>
                <Input
                    title="Комментарий"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_DOOR_CODE}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleDoorCodeKeyPress}
                    value={doorCodeInputValue}
                />
            </StyledThirdRow>
            <StyledFourthRow>
                <SubmitButton
                    view={isSubmitButtonDisabled ? ButtonViews.secondary : ButtonViews.primary}
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_ALL}
                    disabled={isSubmitButtonDisabled}
                    shouldLockAfterClick
                    onClick={handleSubmitClick}
                >
                    Сохранить
                </SubmitButton>
                <StyledVoiceHint>
                    <StyledPlasmaIcon fill="url(#plasma-green-gradient)">
                        <linearGradient id="plasma-green-gradient">
                            <stop offset="0%" stopColor="#00ffdf" />
                            <stop offset="100%" stopColor="#00ff5a" />
                        </linearGradient>
                    </StyledPlasmaIcon>
                    Вы можете изменять данные голосом
                </StyledVoiceHint>
            </StyledFourthRow>
        </StyledRoot>
    );
};
