import React from 'react';
import { KeyboardExpandHandler, useKeyboardExpanded } from 'hooks/useKeyboardExpanded';

interface UseKeyboardCloseParams {
    handleInputSubmit?: () => void;
    handleKeyboardClose?: () => void;
}
export const useKeyboardClose = ({ handleInputSubmit, handleKeyboardClose }: UseKeyboardCloseParams) => {
    /**
     * Для чего нужен этот флаг?
     * Проблема в том, что при нажатии на "ОК" в открытой клавиатуре срабатывает
     * обработчик onSubmit на пнпуте и что-то происходит. Чаще всего это уход с
     * текущего экрана, а так как хуки работают на лайфсайкле реакта, то
     * useKeyboardExpanded может успеть выпилить свои хандлеры по отслеживанию
     * открытия клавиатура, а может и не успеть. Из-за чего иногда (флапает)
     * срабатывает так же хандлер закрытия клавиатуры, что ведет беде.
     * Поэтому при срабатывании одного из хандлеров (закрытие клавы или нажатие на "ОК"),
     * мы выставляем флаг о том, что больше хандлеры вызывать не нужно. Все уже, хватит.
     */
    const [isHandlerInvoked, setHandlerInvoked] = React.useState(false);

    /**
     * А хандлер фокуса инпута нужен для того чтобы понять когда вообще открывается
     * клавиатура, потому что, пока что, единственные способ это узнать, это перехватить
     * фокус на инпуте.
     */
    const onInputFocus = React.useCallback(() => {
        setHandlerInvoked(false);
    }, []);

    const onInputSubmit = React.useCallback(() => {
        if (isHandlerInvoked || !handleInputSubmit) {
            return;
        }

        setHandlerInvoked(true);

        handleInputSubmit();
    }, [handleInputSubmit, isHandlerInvoked, setHandlerInvoked]);

    const handleKeyboardExpand = React.useCallback<KeyboardExpandHandler>(
        (isKeyboardExpanded, keyboardExpandedCount) => {
            if (keyboardExpandedCount === 0 || isKeyboardExpanded) {
                return;
            }

            if (isHandlerInvoked || !handleKeyboardClose) {
                return;
            }

            handleKeyboardClose();
        },
        [isHandlerInvoked, handleKeyboardClose],
    );

    useKeyboardExpanded(handleKeyboardExpand);

    return { onInputFocus, onInputSubmit };
};
