import { createAction, createEmptyAction } from 'tools/create-action';

/* Изменить адрес */
export const addressChangeInputValueAction = (payload: string) => createAction('address/CHANGE_INPUT_VALUE', payload);

/* Выбрать предложенный */
export const addressSelectSuggestItemAction = (payload: string) => createAction('address/SELECT_SUGGEST_ITEM', payload);

/* Ошибка подтверждения */
export const addressSubmitFailAction = () => createEmptyAction('address/SUBMIT_FAIL');

/* Показать подсказку об открытии клавиатуры */
export const addressShowKeyboardHintAction = () => createEmptyAction('address/SHOW_KEYBOARD_HINT');
