import React from 'react';
import styled from 'styled-components';
import { Body1, Footnote1, Headline1 } from '@sberdevices/plasma-styles';
import { primary, secondary, surfaceLiquid01 } from '@sberdevices/plasma-tokens';
import { useFocusOnMount } from 'hooks/useFocusOnMount';

import {
    GuidePagePlasmaIconCommon,
    GuidePageTitleCommon,
    StyledCirclesCommon,
    StyledPlasmaIconCommon,
} from './GuidePage';
import { guidePageSourceCommonMixin } from './GuidePage.mixins';

const circleContainerSize = 316;
const plasmaIconSize = 48;
const offsetToCenteringIcon = circleContainerSize / 2 - plasmaIconSize / 2;
const StyledPlasmaIconSberbox = styled(StyledPlasmaIconCommon)`
    position: absolute;
    height: ${plasmaIconSize}px;
    width: ${plasmaIconSize}px;
    top: ${offsetToCenteringIcon}px;
    left: ${offsetToCenteringIcon}px;
`;

const StyledCirclesSberbox = styled(StyledCirclesCommon)`
    height: ${circleContainerSize}px;
    width: ${circleContainerSize}px;
    margin-top: 106px;

    &::after {
        top: 86px;
        left: 86px;
        right: 86px;
        bottom: 86px;
    }
`;

export const GuidePagePlasmaIconSberbox = () => (
    <GuidePagePlasmaIconCommon sizePx={50}>
        <StyledCirclesSberbox>
            <StyledPlasmaIconSberbox />
        </StyledCirclesSberbox>
    </GuidePagePlasmaIconCommon>
);

export const GuidePageTitleSberbox = styled(GuidePageTitleCommon)`
    margin-top: -54px;
`;

export const GuidePageSourceSberbox = styled(Headline1)`
    margin-top: 32px;

    ${guidePageSourceCommonMixin}
`;

const StyledOkButton = styled(Footnote1)`
    position: relative;
    display: inline-flex;
    height: 78px;
    width: 78px;
    justify-content: center;
    align-items: center;
    color: ${primary};
    margin-left: 12px;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;
        z-index: -1;

        opacity: 0.3;
        background: ${surfaceLiquid01};
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

const GuidePageSubtitle = styled(Body1)`
    margin-top: 130px;
    color: ${secondary};
`;

interface GuidePageHintProps {
    onOkClick: () => void;
}
export const GuidePageHint: React.FC<GuidePageHintProps> = ({ onOkClick }) => {
    const okButtonRef = useFocusOnMount<HTMLDivElement>();

    return (
        <GuidePageSubtitle>
            Чтобы ввести с клавиатуры, нажмите на пульте кнопку
            <StyledOkButton ref={okButtonRef} tabIndex={0} onClick={onOkClick}>
                ОК
            </StyledOkButton>
        </GuidePageSubtitle>
    );
};
