import React from 'react';
import styled from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route } from 'react-router';
import { ConnectedRouter } from 'connected-react-router';
import { history } from 'tools/history';
import { lazyImport } from 'tools/lazy-import';
import { preventScroll } from 'tools/scroll';
import { store } from 'store';
import { ROUTES } from 'store/constants/router';
import { AppLayoutPortal } from 'components/AppLayout/AppLayout@portal';
import { ErrorBoundary } from 'components/ErrorBoundary/ErrorBoundary';
import { Fonts } from 'components/Fonts/Fonts';
import { GlobalResetStyle } from 'components/GlobalResetStyle/GlobalResetStyle';
import { GlobalLayoutVars } from 'components/GlobalLayoutVars/GlobalLayoutVars';
import { LoadingFallbackPortal } from 'components/LoadingFallback/LoadingFallback@portal';
import { ErrorPortal } from 'pages/Error/Error@portal';
import { AddressSimplePortal } from 'pages/AddressSimple/AddressSimple@portal';
import { AddressSimpleGuidePortal } from 'pages/AddressSimpleGuide/AddressSimpleGuide@portal';
import { AddressFullAccommodationGuidePortal } from 'pages/AddressFullAccommodationGuide/AddressFullAccomodationGuide@portal';
import { AddressFullPhoneGuidePortal } from 'pages/AddressFullPhoneGuide/AddressFullPhoneGuide@portal';
import { AddressSimpleQuestionPortal } from 'pages/AddressSimpleQuestion/AddressSimpleQuestion@portal';
import { NoAreaPortal } from 'pages/NoArea/NoArea@portal';
import { PromoPortal } from 'pages/Promo/Promo@portal';
import { RenderErrorPortal } from 'pages/RenderError/RenderError@portal';
import { StatusDetailsPortal } from 'pages/StatusDetails/StatusDetails@portal';
import { StatusEmptyPortal } from 'pages/StatusEmpty/StatusEmpty@portal';
import { StatusReportPortal } from 'pages/StatusReport/StatusReport@portal';
import { StatusSummaryPortal } from 'pages/StatusSummary/StatusSummary@portal';
import { layout } from 'style/const/layout@portal';
import { CartPortal } from 'pages/Cart/Cart@portal';
import { AddressFullAllPortal } from 'pages/AddressFullAll/AddressFullAll@portal';
import { AddressFullApartment } from 'pages/AddressFullApartment/AddressFullApartment';
import { AddressFullDoorCode } from 'pages/AddressFullDoorCode/AddressFullDoorCode';
import { AddressFullEntrance } from 'pages/AddressFullEntrance/AddressFullEntrance';
import { AddressFullFloor } from 'pages/AddressFullFloor/AddressFullFloor';
import { AddressFullPhone } from 'pages/AddressFullPhone/AddressFullPhone';
import { CatalogPortal } from 'pages/Catalog/Catalog@portal';
import { ItemPortal } from 'pages/Item/Item@portal';
import { Categories } from 'pages/Categories/Categories';

const SuccessPortal = lazyImport(
    import(/* webpackChunkName: "Success", webpackPrefetch: 1 */ 'pages/Success/Success@portal'),
);
const WaitPortal = lazyImport(import(/* webpackChunkName: "Wait", webpackPrefetch: 1 */ 'pages/Wait/Wait@portal'));

const StyledContainer = styled.div`
    overflow: hidden;
    width: 100%;
    height: 100vh;
    outline: none;
    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;
    background: transparent;
`;

export const RootPortal: React.FC = () => (
    <ReduxProvider store={store}>
        <GlobalResetStyle />
        <Fonts />
        <GlobalLayoutVars layout={layout} />
        <ConnectedRouter history={history}>
            <StyledContainer onScroll={preventScroll}>
                <ErrorBoundary Layout={AppLayoutPortal} RenderErrorPage={RenderErrorPortal}>
                    <React.Suspense fallback={<LoadingFallbackPortal />}>
                        <AppLayoutPortal>
                            <Switch>
                                <Route path={ROUTES.CATALOG}>
                                    <CatalogPortal />
                                </Route>
                                <Route path={ROUTES.CART}>
                                    <CartPortal />
                                </Route>
                                <Route path={ROUTES.CATEGORIES}>
                                    <Categories />
                                </Route>
                                <Route path={ROUTES.ADDRESS_SIMPLE_GUIDE}>
                                    <AddressSimpleGuidePortal />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL}>
                                    <Switch>
                                        <Route path={ROUTES.ADDRESS_FULL_ALL}>
                                            <AddressFullAllPortal />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_PHONE}>
                                            <AddressFullPhone />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_APARTMENT}>
                                            <AddressFullApartment />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_ENTRANCE}>
                                            <AddressFullEntrance />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_FLOOR}>
                                            <AddressFullFloor />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_DOOR_CODE}>
                                            <AddressFullDoorCode />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_ACCOMMODATION_GUIDE}>
                                            <AddressFullAccommodationGuidePortal />
                                        </Route>
                                        <Route path={ROUTES.ADDRESS_FULL_PHONE_GUIDE}>
                                            <AddressFullPhoneGuidePortal />
                                        </Route>
                                    </Switch>
                                </Route>
                                <Route path={ROUTES.ADDRESS_SIMPLE}>
                                    <AddressSimplePortal />
                                </Route>
                                <Route path={ROUTES.ADDRESS_SIMPLE_QUESTION}>
                                    <AddressSimpleQuestionPortal />
                                </Route>
                                <Route path={ROUTES.NO_AREA}>
                                    <NoAreaPortal />
                                </Route>
                                <Route path={ROUTES.STATUS}>
                                    <Switch>
                                        <Route path={ROUTES.STATUS_SUMMARY}>
                                            <StatusSummaryPortal />
                                        </Route>
                                        <Route path={ROUTES.STATUS_DETAILS}>
                                            <StatusDetailsPortal />
                                        </Route>
                                        <Route path={ROUTES.STATUS_REPORT}>
                                            <StatusReportPortal />
                                        </Route>
                                        <Route path={ROUTES.STATUS_EMPTY}>
                                            <StatusEmptyPortal />
                                        </Route>
                                    </Switch>
                                </Route>
                                <Route path={ROUTES.ERROR}>
                                    <ErrorPortal />
                                </Route>
                                <Route path={ROUTES.SUCCESS}>
                                    <SuccessPortal />
                                </Route>
                                <Route path={ROUTES.WAIT}>
                                    <WaitPortal />
                                </Route>
                                <Route path={ROUTES.PROMO}>
                                    <PromoPortal />
                                </Route>
                                <Route path={ROUTES.ITEM_DETAILS}>
                                    <ItemPortal />
                                </Route>
                            </Switch>
                        </AppLayoutPortal>
                    </React.Suspense>
                </ErrorBoundary>
            </StyledContainer>
        </ConnectedRouter>
    </ReduxProvider>
);
