import React from 'react';
import styled, { css } from 'styled-components';
import { white } from '@sberdevices/plasma-tokens';
import {
    useActiveCategory,
    useCategoryItemImageUrlById,
    useSelectCategoryHandler,
} from 'pages/Categories/Categories.hooks';

export const cardSize = 212;
export const activeCardSize = 302;
export const cardGap = 32;
export const activeCardGap = 64;

interface StyledCategoriesListItemProps {
    imageUrl: string;
    isActive: boolean;
    isHidden: boolean;
}

const StyledCategoriesListItem = styled.div<StyledCategoriesListItemProps>`
    width: ${cardSize}px;
    height: ${cardSize}px;
    margin: 0 auto;

    border-radius: 50%;
    background: no-repeat center / cover url('${({ imageUrl }) => imageUrl}') ${white};

    will-change: transform;
    transform-origin: 50%;
    transition: transform 0.15s ease;

    ${({ isActive }) =>
        isActive &&
        css`
            transform: scale(1.3);
        `}

    ${({ isHidden }) =>
        isHidden &&
        css`
            visibility: hidden;
        `}
`;

interface CategoriesListItemProps {
    id: string;
}

export const CategoriesListItem: React.FC<CategoriesListItemProps> = ({ id }) => {
    const activeCategory = useActiveCategory();
    const selectItem = useSelectCategoryHandler(id);
    const imageUrl = useCategoryItemImageUrlById(id);

    const isActive = activeCategory?.id === id;
    const isHidden = !imageUrl;

    return (
        <StyledCategoriesListItem imageUrl={imageUrl} isActive={isActive} isHidden={isHidden} onClick={selectItem} />
    );
};
