/* eslint-disable @typescript-eslint/camelcase */
import { doDGApiRequest } from 'tools/twoGis/api/doApiRequest';
import { DGListResult } from 'tools/twoGis/api/common';
import { DGBuildingSuggest } from 'tools/twoGis/api/suggest';

type DGApiGetItemsGeocodeParams = {
    point: string;
    radius: number;
    type: string[];
    fields: string[];
};

type DGGetItemsGeocodeParams = {
    point: string;
};

const geoSearchEndpointPath = '/3.0/items/geocode';

export const getGeocode = async ({ point }: DGGetItemsGeocodeParams) => {
    return doDGApiRequest<DGApiGetItemsGeocodeParams, DGListResult<DGBuildingSuggest>>(geoSearchEndpointPath, {
        point,
        radius: 30,
        type: ['building'],
        fields: ['items.address', 'items.point'],
    });
};
