import React from 'react';
import { goBack } from 'connected-react-router';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { icon } from 'style/const/icon';

import { ReactComponent as ChevronLeftSvg } from '../Header.assets/chevron-left.svg';

const backButtonIconSizePx = icon.m.sizePx;

const StyledChevronLeft = styled(ChevronLeftSvg)`
    width: ${backButtonIconSizePx}px;
    height: ${backButtonIconSizePx}px;
`;

export const StyledBackButton = styled.span`
    position: absolute;
    top: 50px;
    left: 24px;

    width: 80px;
    height: 80px;

    display: flex;
    align-items: center;
    justify-content: center;
`;

export const HeaderBackButtonPortal: React.FC = (props) => {
    const dispatch = useDispatch();

    const handleClick = React.useCallback(() => {
        // TODO: вызывать closeAppAction при пустой истории FOODTECHUI-393
        dispatch(goBack());
    }, [dispatch]);

    return (
        <StyledBackButton onClick={handleClick} {...props}>
            <StyledChevronLeft />
        </StyledBackButton>
    );
};
