import { ConnectedRouter } from 'connected-react-router';
import React from 'react';
import styled from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route } from 'react-router';
import { history } from 'tools/history';
import { lazyImport } from 'tools/lazy-import';
import { preventScroll } from 'tools/scroll';
import { ErrorBoundary } from 'components/ErrorBoundary/ErrorBoundary';
import { Fonts } from 'components/Fonts/Fonts';
import { GlobalResetStyle } from 'components/GlobalResetStyle/GlobalResetStyle';
import { GlobalLayoutVars } from 'components/GlobalLayoutVars/GlobalLayoutVars';
import { AppLayoutTouch } from 'components/AppLayout/AppLayout@touch';
import { LoadingFallbackTouch } from 'components/LoadingFallback/LoadingFallback@touch';
import { PromoTouch } from 'pages/Promo/Promo@touch';
import { RenderErrorSberbox } from 'pages/RenderError/RenderError@sberbox';
import { store } from 'store';
import { ROUTES } from 'store/constants/router';
import { layout } from 'style/const/layout@touch';
import { AddressMapTouch } from 'pages/AddressMap/AddressMap@touch';

const AddressFullAllTouch = lazyImport(
    import(/* webpackChunkName: "AddressFullAll", webpackPrefetch: 1 */ 'pages/AddressFullAll/AddressFullAll@touch'),
);
const CartTouch = lazyImport(import(/* webpackChunkName: "Cart", webpackPrefetch: 1 */ 'pages/Cart/Cart@touch'));
const CatalogTouch = lazyImport(
    import(/* webpackChunkName: "Catalog", webpackPrefetch: 1 */ 'pages/Catalog/Catalog@touch'),
);
const SuccessTouch = lazyImport(
    import(/* webpackChunkName: "Success", webpackPrefetch: 1 */ 'pages/Success/Success@touch'),
);

const StyledContainer = styled.div`
    overflow: hidden;

    width: 100%;
    height: 100vh;

    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;

    outline: none;
    background: #000;
`;

export const RootTouch: React.FC = () => (
    <ReduxProvider store={store}>
        <GlobalResetStyle />
        <Fonts />
        <GlobalLayoutVars layout={layout} />
        <ConnectedRouter history={history}>
            <StyledContainer onScroll={preventScroll}>
                <ErrorBoundary Layout={AppLayoutTouch} RenderErrorPage={RenderErrorSberbox}>
                    <React.Suspense fallback={<LoadingFallbackTouch />}>
                        <AppLayoutTouch>
                            <Switch>
                                <Route path={ROUTES.CATALOG}>
                                    <CatalogTouch />
                                </Route>
                                <Route path={ROUTES.CART}>
                                    <CartTouch />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_ALL}>
                                    <AddressFullAllTouch />
                                </Route>
                                <Route path={ROUTES.SUCCESS}>
                                    <SuccessTouch />
                                </Route>
                                <Route path={ROUTES.ADDRESS_SIMPLE}>
                                    <AddressMapTouch />
                                </Route>
                                <Route path={ROUTES.PROMO}>
                                    <PromoTouch />
                                </Route>
                            </Switch>
                        </AppLayoutTouch>
                    </React.Suspense>
                </ErrorBoundary>
            </StyledContainer>
        </ConnectedRouter>
    </ReduxProvider>
);
