import { createAction, createEmptyAction } from 'tools/create-action';
import { AppInfo, Character } from 'tools/assistant';

import {
    ShowItemsCommandPayload,
    ShowCategoriesCommandPayload,
    AddItemCommandPayload,
    RemoveItemCommandPayload,
    GoToCatalogCommandPayload,
    ValidationOkCommandPayload,
    AddressInputPayload,
    AddressInputAddPayload,
    AddressFullInputAddPayload,
    AddressFullInputCommandPayload,
    OrderErrorPayload,
    ItemsChangesCommandPayload,
    AddressFullCommentCommandPayload,
    GoToPromoCommandPayload,
    AddressFullInputClearPayload,
    LastOrderInfoPayload,
    GoToCategoriesCommandPayload,
    GoToNoAreaCommandPayload,
    OrderChangedCommandPayload,
} from '../constants/native';

/* Показать список товаров */
export const showItemsAction = (payload: ShowItemsCommandPayload) => createAction('SHOW_ITEMS', payload);

/* Показать данные о категориях */
export const showCategoriesAction = (payload: ShowCategoriesCommandPayload) => createAction('SHOW_CATEGORIES', payload);

/* Изменился список товаров */
export const itemsChangesAction = (payload: ItemsChangesCommandPayload) => createAction('ITEMS_CHANGES', payload);

/* Добавить товар в корзину */
export const addItemAction = (payload: AddItemCommandPayload) => createAction('ADD_ITEM', payload);

interface RemoveItemCommandPayloadExtended extends RemoveItemCommandPayload {
    shouldRemoveMention?: boolean;
}

/* Удалить товар из корзины */
export const removeItemAction = (payload: RemoveItemCommandPayloadExtended) => createAction('REMOVE_ITEM', payload);

/* Очистить корзину */
export const removeAllAction = () => createEmptyAction('REMOVE_ALL');

/* Перейти на страницу каталога */
export const goToCatalogAction = (payload: GoToCatalogCommandPayload) => createAction('GO_TO_CATALOG', payload);

/* Перейти на страницу категорий */
export const goToCategoriesAction = (payload: GoToCategoriesCommandPayload) =>
    createAction('GO_TO_CATEGORIES', payload);

/* Перейти на страницу "вне зоны покрытия" */
export const goToNoAreaAction = (payload: GoToNoAreaCommandPayload) => createAction('GO_TO_NO_AREA', payload);

/* Перейти на страницу промо */
export const goToPromo = (payload: GoToPromoCommandPayload) => createAction('GO_TO_PROMO', payload);

/* Перейти на предыдущую страницу */
export const goBackAction = () => createEmptyAction('GO_BACK');

/* Перейти на страницу "оформляем заказ" */
export const goToWaitAction = () => createEmptyAction('GO_TO_WAIT');

/* Заказ оформлен */
export const validationOkAction = (payload: ValidationOkCommandPayload) => createAction('VALIDATION_OK', payload);

/* Ошибка оформления заказа */
export const orderErrorAction = (payload: OrderErrorPayload) => createAction('ORDER_ERROR', payload);

/* Ввод адреса */
export const addressInputAction = (payload: AddressInputPayload) => createAction('ADDRESS_INPUT', payload);

/* Экран голосового ввода адреса */
export const addressGuideAction = () => createEmptyAction('ADDRESS_GUIDE');

/* Дополнить существующий адрес */
export const addressInputAddAction = (payload: AddressInputAddPayload) => createAction('ADDRESS_INPUT_ADD', payload);

/* Отказ от показанного адреса, выбрать другой */
export const addressInputDeclineAction = () => createEmptyAction('ADDRESS_INPUT_DECLINE');

/* Очистить поле ввода адреса */
export const addressInputClearAction = () => createEmptyAction('ADDRESS_INPUT_CLEAR');

/* Ввод полного адреса */
export const addressFullInputAction = (payload: AddressFullInputCommandPayload) =>
    createAction('ADDRESS_FULL_INPUT', payload);

/* Добавить полный адрес */
export const addressFullInputAddAction = (payload: AddressFullInputAddPayload) =>
    createAction('ADDRESS_FULL_INPUT_ADD', payload);

/* Очистить полный адрес */
export const addressFullInputClearAction = (payload: AddressFullInputClearPayload) =>
    createAction('ADDRESS_FULL_INPUT_CLEAR', payload);

/* Комментарий полного адреса */
export const addressFullCommentAction = (payload: AddressFullCommentCommandPayload) =>
    createAction('ADDRESS_FULL_COMMENT', payload);

/* Ошибка валидации полного адреса */
export const addressFullValidationErrorAction = () => createEmptyAction('ADDRESS_FULL_VALIDATION_ERROR');

/* Успешное сохранение адреса */
export const addressFullSuccessAction = () => createEmptyAction('ADDRESS_FULL_SUCCESS');

/* Повторный запрос статуса */
export const askAgainForOrderStatusAction = () => createEmptyAction('ASK_AGAIN_FOR_ORDER_STATUS');

/* Второй этап оформления - после оплаты */
export const askAgainForOrderStatusFinalAction = () => createEmptyAction('ASK_AGAIN_FOR_ORDER_STATUS_FINAL');

/* Заказ создан */
export const orderSuccessAction = () => createEmptyAction('ORDER_SUCCESS');

/* Заказ изменился во время оформления */
export const orderChangedAction = (payload: OrderChangedCommandPayload) => createAction('ORDER_CHANGED', payload);

export const setCharacterAction = (payload: Character) => createAction('character/SET_CHARACTER', payload);

export const setAppContextAction = (payload: AppInfo) => createAction('appContext/SET_APP_CONTEXT', payload);

/* Флоу статуса заказа */

/* Статус последнего заказа */
export const lastOrderInfoAction = (payload: LastOrderInfoPayload) => createAction('LAST_ORDER_INFO', payload);

/* Заказ не найден */
export const lastOrderEmptyAction = () => createEmptyAction('LAST_ORDER_EMPTY');

/* Системная ошибка при получении статуса заказа */
export const lastOrderErrorAction = () => createEmptyAction('LAST_ORDER_ERROR');
