import React from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullEntranceInputChangeAction } from 'store/actions/addressFullPage';

export const useFullEntrance = () => {
    const dispatch = useDispatch();

    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullEntranceInputChangeAction(e.target.value));
        },
        [dispatch],
    );

    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.entranceInputValue);

    return { inputValue, handleInputChange };
};
