import React from 'react';
import styled, { css } from 'styled-components';
import { Button2 } from '@sberdevices/plasma-styles';
import { buttonSecondary, primary } from '@sberdevices/plasma-tokens';
import { transition } from 'style/const/transition';
import { icon } from 'style/const/icon';
import { focusBorderMixin } from 'style/mixin/focus';

const containerBorderRadiusPx = 24;

interface StyledHeaderButtonProps {
    isVisible?: boolean;
}

const StyledHeaderButton = styled.button<StyledHeaderButtonProps>`
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 0 0 15px;

    box-sizing: border-box;
    height: 32px;
    min-width: 32px;
    padding: 0;
    border: none;
    border-radius: ${containerBorderRadiusPx}px;

    background: transparent;

    font-family: inherit;

    transition: background-color ${transition.default};
    will-change: background-color;

    ${focusBorderMixin({ borderRadius: containerBorderRadiusPx })}

    ${({ isVisible }) =>
        !isVisible &&
        css`
            display: none;
        `}

    &:focus {
        background: ${buttonSecondary};
    }

    &:first-of-type {
        margin-left: 0;
    }
`;

const textHeightPx = icon.xs.sizePx;

const StyledText = styled(Button2)`
    margin-left: 8px;

    height: ${textHeightPx}px;
    padding: 0 7px 0 1px;

    line-height: ${textHeightPx}px;

    color: ${primary};

    opacity: 0.88;
`;

interface HeaderButtonProps {
    text?: string;
    isVisible?: boolean;
    onClick?: () => void;
}

export const HeaderButtonTouch: React.FC<HeaderButtonProps> = ({ text, isVisible, onClick: handleClick, children }) => {
    return (
        <StyledHeaderButton tabIndex={0} onClick={handleClick} isVisible={isVisible} type="button">
            {children}
            {text ? <StyledText>{text}</StyledText> : null}
        </StyledHeaderButton>
    );
};
