import React from 'react';
import styled from 'styled-components';
import { Body1, Caption, Headline2, ParagraphText2 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { Pin, HappyFaceIcon } from 'components/Pin/Pin';

export const CartInfoContainerSberbox = styled.div`
    position: relative;

    width: 744px;
    padding-top: 48px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledTime = styled(ParagraphText2)`
    color: ${primary};
`;

const StyledUnit = styled(Caption)`
    margin-top: -10px;

    color: ${primary};
`;

interface CartInfoProps {
    time?: number;
}

export const CartInfoPinSberbox: React.FC<CartInfoProps> = ({ time }) => (
    <Pin pinColor="good">
        {time !== undefined ? (
            <>
                <StyledTime>{time}</StyledTime>
                <StyledUnit>мин</StyledUnit>
            </>
        ) : (
            <HappyFaceIcon />
        )}
    </Pin>
);

const StyledPersonal = styled.div`
    margin-top: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledPersonalLocation = styled(Body1)`
    -webkit-line-clamp: 2;

    color: ${primary};
`;

const StyledPersonalPhoneNumber = styled(Headline2)`
    margin-top: 60px;

    color: ${primary};
`;

const StyledPersonalName = styled(Body1)`
    margin-top: 8px;

    color: ${primary};
`;

interface CartPersonalProps {
    address: string;
    phone: string;
    name?: string;
}

export const CartPersonalSberbox: React.FC<CartPersonalProps> = ({ address, phone, name }) => (
    <StyledPersonal>
        <StyledPersonalLocation>{address}</StyledPersonalLocation>
        <StyledPersonalPhoneNumber>{phone}</StyledPersonalPhoneNumber>
        {name ? <StyledPersonalName>{name}</StyledPersonalName> : null}
    </StyledPersonal>
);

export const CartAddressSberbox = styled(Headline2)`
    margin-top: 48px;

    display: flex;
    flex-direction: column;
    align-items: center;

    -webkit-line-clamp: 2;

    color: ${primary};
`;
