import React from 'react';
import styled from 'styled-components';
import { Body1, Headline1 } from '@sberdevices/plasma-styles';
import { Button } from 'components/Button/Button';
import { layout } from 'style/const/layout@sberbox';

import {
    LocationBackgroundCommon,
    LocationPageCommon,
    LocationPageProps,
    StyledButtonContainerCommon,
    styledText,
    styledTitle,
} from './LocationPage';

export const LocationMapBackgroundSberbox = styled(LocationBackgroundCommon)`
    top: -${layout.headerHeight}px;

    height: ${layout.height}px;
`;

const StyledTitle = styledTitle(Headline1);

export const LocationTitleSberbox = styled(StyledTitle)`
    margin-top: 48px;
`;

export const LocationTextSberbox = styledText(Body1);

export const LocationButtonSberbox = styled(Button)`
    margin: 0 16px;
`;

const StyledButtonContainer = styled(StyledButtonContainerCommon)`
    margin-top: 64px;
`;

export const LocationPageSberbox: React.FC<LocationPageProps> = ({ children, buttons, background }) => {
    const renderButtonContainer = React.useCallback(() => <StyledButtonContainer>{buttons}</StyledButtonContainer>, [
        buttons,
    ]);

    return (
        <LocationPageCommon background={background} buttons={buttons} renderButtonContainer={renderButtonContainer}>
            {children}
        </LocationPageCommon>
    );
};
