import React from 'react';
import styled from 'styled-components';
import { Headline2 } from '@sberdevices/plasma-styles';

import { useActiveCategory } from '../Categories.hooks';

const StyledName = styled(Headline2)`
    width: 100%;
    margin-top: 40px;

    text-align: center;
`;

export const CategoriesName: React.FC = () => {
    const category = useActiveCategory();

    if (!category) {
        return null;
    }

    return <StyledName>{category.name}</StyledName>;
};
