import React from 'react';
import styled from 'styled-components';
import { Footnote1, Headline2 } from '@sberdevices/plasma-styles';
import { Button } from 'components/Button/Button';
import { layout } from 'style/const/layout@portal';

import {
    LocationBackgroundCommon,
    LocationPageCommon,
    LocationPageProps,
    StyledButtonContainerCommon,
    styledText,
    styledTitle,
} from './LocationPage';

export const LocationMapBackgroundPortal = styled(LocationBackgroundCommon)`
    top: -${layout.headerHeight}px;

    height: ${layout.height}px;
`;

const StyledTitle = styledTitle(Headline2);

export const LocationTitlePortal = styled(StyledTitle)`
    margin-top: 32px;
`;

export const LocationTextPortal = styledText(Footnote1);

export const LocationButtonPortal = styled(Button)`
    margin: 0 12px;
    padding: 24px 40px;
`;

const StyledButtonContainer = styled(StyledButtonContainerCommon)`
    margin-top: 48px;
`;

export const LocationPagePortal: React.FC<LocationPageProps> = ({ children, buttons, background }) => {
    const renderButtonContainer = React.useCallback(() => <StyledButtonContainer>{buttons}</StyledButtonContainer>, [
        buttons,
    ]);

    return (
        <LocationPageCommon background={background} buttons={buttons} renderButtonContainer={renderButtonContainer}>
            {children}
        </LocationPageCommon>
    );
};
