import { AppState, AppReducer, initialState } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import {
    canAddMore,
    getCountById,
    getDisabled,
    getOldPriceById,
    prepareBadge,
    preparePriceById,
    wantMoreThanHave,
} from 'store/reducers/helpers/catalogPage';
import { prepareIndex } from 'store/reducers/view/catalogPage';
import { selectPathname, selectProductById } from 'store/selectors';

export const prepareCatalogItemsMap = (state: AppState) => {
    const {
        catalogPagePortal: { order },
    } = state;

    return order.reduce<AppState['catalogPagePortal']['itemsMap']>((acc, itemId) => {
        const { name, imageUrl } = selectProductById(state, itemId);

        acc[itemId] = {
            imageUrl,
            name,
            price: preparePriceById(state, itemId),
            oldPrice: getOldPriceById(state, itemId),
            isWantMoreThanHave: wantMoreThanHave(state, itemId),
            count: getCountById(state, itemId),
            index: prepareIndex(state, itemId),
            badge: prepareBadge(state, itemId),
            isButtonDisabled: !canAddMore(state, itemId),
            disabled: getDisabled(state, itemId),
        };

        return acc;
    }, {});
};

const getScreenIndexMap = ({ catalogPagePortal: { listIndexOffset, order } }: AppState): Record<string, number> =>
    new Array(3).fill(0).reduce(
        (result, _, index) => ({
            ...result,
            [order[index + listIndexOffset]]: index + 1,
        }),
        {} as Record<string, number>,
    );

export const catalogPagePortalView: AppReducer = (state, action) => {
    const { catalogPagePortal } = state;

    switch (action.type) {
        case 'SHOW_ITEMS': {
            catalogPagePortal.order = action.payload.stock.map((productItem) => productItem.id);

            const [firstItemId] = catalogPagePortal.order;
            catalogPagePortal.selectedItemId = firstItemId;
            catalogPagePortal.listIndexOffset = initialState.catalogPagePortal.listIndexOffset;

            break;
        }
        case 'ADD_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                if (catalogPagePortal.order.includes(action.payload.item)) {
                    catalogPagePortal.selectedItemId = action.payload.item;

                    if (!action.payload.skipFocus) {
                        catalogPagePortal.listIndexOffset = catalogPagePortal.order.findIndex(
                            (itemId) => itemId === action.payload.item,
                        );
                    }
                }
            }

            break;
        }

        case 'REMOVE_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                catalogPagePortal.selectedItemId = action.payload.item;
                catalogPagePortal.listIndexOffset = catalogPagePortal.order.findIndex(
                    (itemId) => itemId === action.payload.item,
                );
            }

            break;
        }

        case 'REMOVE_ALL': {
            catalogPagePortal.selectedItemId = initialState.catalogPagePortal.selectedItemId;
            catalogPagePortal.listIndexOffset = initialState.catalogPagePortal.listIndexOffset;

            break;
        }

        case 'catalogPortal/LIST_SCROLL': {
            catalogPagePortal.listIndexOffset = action.payload;
            catalogPagePortal.selectedItemId = catalogPagePortal.order[action.payload];

            break;
        }

        default:
            break;
    }

    catalogPagePortal.itemsMap = prepareCatalogItemsMap(state);
    catalogPagePortal.screenIndexMap = getScreenIndexMap(state);
};
