import { AppState } from '../constants';

export const selectProductById = (state: AppState, itemId: string) => state.productsData.itemsMap[itemId];
export const selectProductDiffById = (state: AppState, itemId: string) => state.productsDiffData.itemsMap[itemId];
export const selectCartItemById = (state: AppState, itemId: string) => state.cart.itemsMap[itemId];
export const selectCategoryById = (state: AppState, itemId: string) => state.categoriesData.itemsMap[itemId];
export const selectPathname = (state: AppState) => state.router.location.pathname;
export const selectCharacter = (state: AppState) => state.character.id;
