import React from 'react';
import { ButtonViews } from 'components/Button/Button';
import {
    LocationButtonSberbox,
    LocationMapBackgroundSberbox,
    LocationPageSberbox,
    LocationTextSberbox,
    LocationTitleSberbox,
} from 'components/LocationPage/LocationPage@sberbox';

import { NoAreaPin } from './NoArea';
import { useNoAreaPage } from './NoArea.hooks';

export const NoAreaSberbox: React.FC = () => {
    const { addressString, handleChangeButtonClick, handleCloseButtonClick } = useNoAreaPage();

    const buttons = React.useMemo(
        () => (
            <>
                <LocationButtonSberbox view={ButtonViews.primary} shouldFocusOnMount onClick={handleChangeButtonClick}>
                    Ввести другой
                </LocationButtonSberbox>
                <LocationButtonSberbox onClick={handleCloseButtonClick} view={ButtonViews.secondary}>
                    Не сейчас
                </LocationButtonSberbox>
            </>
        ),
        [handleChangeButtonClick, handleCloseButtonClick],
    );

    const background = React.useMemo(() => <LocationMapBackgroundSberbox />, []);

    return (
        <LocationPageSberbox buttons={buttons} background={background}>
            <NoAreaPin />
            <LocationTitleSberbox>{addressString}</LocationTitleSberbox>
            <LocationTextSberbox>К сожалению, пока не доставляем по этому адресу</LocationTextSberbox>
        </LocationPageSberbox>
    );
};
