import { goBack, push, replace } from 'connected-react-router';
import { addressSubmitFailAction } from 'store/actions/addressSimplePage';
import {
    addressGuideAction,
    addressInputAction,
    addressInputAddAction,
    addressInputDeclineAction,
    goToCatalogAction,
    goToCategoriesAction,
    goToNoAreaAction,
    goToPromo,
    showItemsAction,
    validationOkAction,
} from 'store/actions/native';
import { goBackToStart } from 'store/actions/router';
import { addressSubmitAction } from 'store/actions/server';
import { AppThunkAction, scenarioResponseTimeout } from 'store/constants';
import {
    AddressInputAddPayload,
    AddressInputPayload,
    GoToCatalogCommandPayload,
    GoToCategoriesCommandPayload,
    GoToNoAreaCommandPayload,
    GoToPromoCommandPayload,
    ShowItemsCommandPayload,
    ValidationOkCommandPayload,
} from 'store/constants/native';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';
import { DeviceFamily, deviceFamily, isAllNavigationBetweenScreensDisabled } from 'tools/environment';
import { getWaitNextNavigationEventPromise } from 'tools/history';

export const showItemsThunk = (payload: ShowItemsCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(showItemsAction(payload));
    dispatch(push(ROUTES.CATALOG));
};

export const goBackToStartAndReplaceThunk = (route: ROUTES): AppThunkAction => async (dispatch) => {
    if (window.history.length > 1) {
        dispatch(goBackToStart());

        await getWaitNextNavigationEventPromise();
    }

    dispatch(replace(route));
};

export const goToCatalogThunk = (payload: GoToCatalogCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(goToCatalogAction(payload));

    if (isAllNavigationBetweenScreensDisabled) {
        return;
    }

    dispatch(goBackToStartAndReplaceThunk(ROUTES.CATALOG));
};

export const goToCategoriesThunk = (payload: GoToCategoriesCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(goToCategoriesAction(payload));

    if (isAllNavigationBetweenScreensDisabled) {
        return;
    }

    dispatch(goBackToStartAndReplaceThunk(ROUTES.CATEGORIES));
};

export const goToNoAreaThunk = (payload: GoToNoAreaCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(goToNoAreaAction(payload));
    dispatch(push(ROUTES.NO_AREA));
};

export const goToPromoThunk = (payload: GoToPromoCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(goToPromo(payload));

    // чтобы при перезагрузке страницы постоянно не переходить в промо
    if (isAllNavigationBetweenScreensDisabled) {
        return;
    }

    dispatch(replace(ROUTES.PROMO));
};

export const validationOkThunk = (payload: ValidationOkCommandPayload): AppThunkAction => (dispatch) => {
    dispatch(validationOkAction(payload));

    dispatch(goBackToStartAndReplaceThunk(ROUTES.SUCCESS));
};

export const addressInputThunk = (payload: AddressInputPayload): AppThunkAction => (dispatch) => {
    dispatch(addressInputAction(payload));

    // чтобы при перезагрузке страницы постоянно не переходить на страницу с вопросом
    if (isAllNavigationBetweenScreensDisabled) {
        return;
    }

    dispatch(push(ROUTES.ADDRESS_SIMPLE_QUESTION));
};

export const addressSubmitThunk = (): AppThunkAction => (dispatch) => {
    // TODO: убрать таймаут и сделать на промисах
    // https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-269
    const timerId = setTimeout(() => {
        dispatch(addressSubmitFailAction());
    }, scenarioResponseTimeout);

    dispatch(addressSubmitAction(timerId));
};

export const addressGuideThunk = (): AppThunkAction => (dispatch) => {
    dispatch(addressGuideAction());

    if (deviceFamily === DeviceFamily.MOBILE) {
        dispatch(push(ROUTES.ADDRESS_SIMPLE));
    } else {
        dispatch(push(ROUTES.ADDRESS_SIMPLE_GUIDE));
    }
};

export const addressInputDeclineThunk = (): AppThunkAction => (dispatch, getState) => {
    const {
        addressSimplePage: { suggests },
    } = getState();
    const totalSuggestsCount = suggests.length;

    dispatch(addressInputDeclineAction());

    if (totalSuggestsCount > 1) {
        dispatch(replace(ROUTES.ADDRESS_SIMPLE));
    } else {
        dispatch(goBack());
    }
};

export const addressInputAddThunk = (payload: AddressInputAddPayload): AppThunkAction => (dispatch) => {
    dispatch(addressInputAddAction(payload));
    dispatch(push(ROUTES.ADDRESS_SIMPLE_QUESTION));
};

export const goToAddressFullAllThunk = (): AppThunkAction => (dispatch, getState) => {
    const state = getState();
    const {
        addressFullPage: { isSubmitButtonDisabled },
    } = state;

    // значит мы заходим не впервые и нужно показывать все инпуты
    if (!isSubmitButtonDisabled) {
        dispatch(push(ROUTES.ADDRESS_FULL_ALL));

        return;
    }

    dispatch(push(ROUTES.ADDRESS_FULL_PHONE_GUIDE));
};

export const goToValidationThunk = (): AppThunkAction => (dispatch, getState) => {
    const state = getState();
    const currentPathname = selectPathname(state);

    switch (currentPathname) {
        case ROUTES.WAIT: {
            dispatch(goBack());
            break;
        }

        default: {
            dispatch(push(ROUTES.CART));
            break;
        }
    }
};

export const paymentCancelledByUserThunk = (): AppThunkAction => (dispatch, getState) => {
    const state = getState();
    const currentPathname = selectPathname(state);

    if (currentPathname === ROUTES.WAIT) {
        dispatch(goBack());
    }
};
