import React from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullSubmitAction } from 'store/actions/server';
import { ROUTES } from 'store/constants/router';
import { usePressHandler } from 'hooks/usePressHandler';

export const useAddressFull = () => {
    const dispatch = useDispatch();

    const {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
        nextFocus,
        isPhoneValid,
    } = useSelector(({ addressFullPage }) => addressFullPage);

    const handleAddressKeyPress = usePressHandler(ROUTES.ADDRESS_SIMPLE_GUIDE);

    const handlePhoneKeyPress = usePressHandler(ROUTES.ADDRESS_FULL_PHONE);

    const handleApartmentKeyPress = usePressHandler(ROUTES.ADDRESS_FULL_APARTMENT);

    const handleEntranceKeyPress = usePressHandler(ROUTES.ADDRESS_FULL_ENTRANCE);

    const handleFloorKeyPress = usePressHandler(ROUTES.ADDRESS_FULL_FLOOR);

    const handleDoorCodeKeyPress = usePressHandler(ROUTES.ADDRESS_FULL_DOOR_CODE);

    const handleSubmitClick = React.useCallback(() => {
        dispatch(addressFullSubmitAction());
    }, [dispatch]);

    return {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
        nextFocus,
        isPhoneValid,
        handleAddressKeyPress,
        handlePhoneKeyPress,
        handleApartmentKeyPress,
        handleEntranceKeyPress,
        handleFloorKeyPress,
        handleDoorCodeKeyPress,
        handleSubmitClick,
    };
};
