import { AppReducer } from 'store/constants';
import { ErrorType } from 'store/constants/error';
import { OrderErrorPayload } from 'store/constants/native';

const mapOrderErrorType: Record<OrderErrorPayload['type'], ErrorType> = {
    partner: ErrorType.ORDER_PARTNER,
    timing: ErrorType.ORDER_TIMING,
};

export const errorModel: AppReducer = (state, action) => {
    switch (action.type) {
        case 'ORDER_ERROR': {
            state.error.type = mapOrderErrorType[action.payload.type];
            break;
        }
        case 'LAST_ORDER_ERROR': {
            state.error.type = ErrorType.STATUS;
            break;
        }
        case 'ERROR/reset': {
            state.error.type = ErrorType.NONE;
            break;
        }
        default: {
            break;
        }
    }
};
