import React from 'react';
import styled, { css } from 'styled-components';
import { Body1, Footnote1 } from '@sberdevices/plasma-styles';
import { primary, secondary } from '@sberdevices/plasma-tokens';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { Label, LabelProps } from 'components/Label/Label';

const StyledIndexBadge = styled(IndexBadge)`
    top: 4px;
    right: 4px;
`;

const thumbSizePx = 120;

interface StyledThumbProps {
    imageUrl: string;
}

const StyledThumb = styled.div<StyledThumbProps>`
    position: relative;

    margin-right: 32px;

    height: ${thumbSizePx}px;
    width: ${thumbSizePx}px;

    border-radius: 24px;

    background-image: url('${({ imageUrl }) => imageUrl}');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
`;

const StyledInfo = styled.div`
    margin-right: 48px;

    width: 384px;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledNameProps {
    isOneLine?: boolean;
}
const StyledName = styled(Body1)<StyledNameProps>`
    overflow: hidden;

    ${({ isOneLine }) =>
        isOneLine &&
        css`
            white-space: nowrap;
            text-overflow: ellipsis;
        `}

    ${({ isOneLine }) =>
        !isOneLine &&
        css`
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
        `}

    color: ${primary};
`;

const StyledPriceContainer = styled.div`
    margin-top: 4px;

    display: flex;
`;

const StyledPrice = styled(Footnote1)`
    color: ${secondary};
`;

const StyledOldPrice = styled(Footnote1)`
    position: relative;
    opacity: 0.24;
    color: ${primary};
    margin-left: 8px;

    &::before {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        border-top: 4px solid ${primary};
        left: 2px;
        right: 2px;
    }
`;

const StyledLabel = styled(Label)`
    margin-top: 4px;
`;

interface StyledCartListItemProps {
    disabled?: boolean;
}

export const CartListItemRootSberbox = styled.div<StyledCartListItemProps>`
    position: relative;

    box-sizing: border-box;
    height: 152px;
    width: 832px;
    padding: 16px;

    display: flex;
    align-items: center;

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledThumb},
            ${StyledName},
            ${StyledPrice} {
                opacity: .24;
            }

            ${StyledPrice} {
                color: ${primary};
            }
        `}
`;
interface CartListItemThumbProps {
    imageUrl: string;
    index?: number;
}

const StyledCount = styled(Footnote1)`
    color: ${secondary};

    &::before {
        content: '×';
    }
`;

export const CartListItemThumbSberbox: React.FC<CartListItemThumbProps> = ({ imageUrl, index }) => (
    <StyledThumb imageUrl={imageUrl}>{index !== undefined && <StyledIndexBadge size="s" index={index} />}</StyledThumb>
);

interface CartListItemInfoProps {
    disabled?: boolean;
    label?: LabelProps;
    name: string;
    price: number;
    oldPrice?: number;
    count?: number;
}

export const CartListItemInfoSberbox: React.FC<CartListItemInfoProps> = ({
    disabled,
    label,
    name,
    price,
    oldPrice,
    count,
}) => {
    const prices = React.useMemo(
        () =>
            [
                <StyledPrice>{price} ₽</StyledPrice>,
                oldPrice && <StyledOldPrice>{oldPrice} ₽</StyledOldPrice>,
                count && <StyledCount>{count}</StyledCount>,
            ]
                .filter(Boolean)
                .map((item, index) => (
                    <>
                        {index > 0 && <>&nbsp;</>}
                        {item}
                    </>
                )),
        [price, oldPrice, count],
    );

    return (
        <StyledInfo>
            <StyledName isOneLine={disabled || Boolean(label)}>{name}</StyledName>
            <StyledPriceContainer>{prices}</StyledPriceContainer>
            {label ? <StyledLabel {...label} /> : null}
        </StyledInfo>
    );
};
