import { goBack } from 'connected-react-router';
import React from 'react';
import Swiper from 'swiper';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { startFetchingTwoGisSuggests, finishFetchingTwoGisSuggests } from 'store/actions/twoGisSuggests';
import { TwoGisSuggestFetchingStatus } from 'store/reducers/data/twoGisSuggestsData';
import { getSuggestsList } from 'tools/twoGis/getSuggestsList';

import { SWIPE_SPEED } from './AddressSimple.constants';

interface SwiperOptions {
    paddingForFocus: number;
    spaceBetween: number;
    suggestIds: string[];
    suggestsListIndex: number;
}

export const useSwiper = ({ paddingForFocus, spaceBetween, suggestIds, suggestsListIndex }: SwiperOptions) => {
    const swiperContainerRef = React.useRef<HTMLDivElement>(null);
    const swiperRef = React.useRef<Swiper | null>(null);

    React.useLayoutEffect(() => {
        if (swiperContainerRef.current) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper(swiperContainerRef.current, {
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    speed: SWIPE_SPEED,
                    spaceBetween,
                    initialSlide: 0,
                    slidesOffsetBefore: paddingForFocus,
                    slidesOffsetAfter: paddingForFocus + 52,
                });
        }
    }, [paddingForFocus, spaceBetween, swiperContainerRef]);

    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.updateSize();
            swiperRef.current.updateSlides();
            swiperRef.current.updateProgress();
            swiperRef.current.updateSlidesClasses();
        }
    }, [suggestIds]);

    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.slideTo(suggestsListIndex - 1, SWIPE_SPEED);
        }
    }, [suggestsListIndex]);

    return swiperContainerRef;
};

export const useAddressSimpleUpdateSuggests = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
    const latitude = useSelector(({ userData }) => userData.deviceLocationLatitude);
    const longitude = useSelector(({ userData }) => userData.deviceLocationLongitude);
    const suggestsFetchingStatus = useSelector(({ twoGisSuggestsData }) => twoGisSuggestsData.fetchingStatus);

    React.useEffect(() => {
        if (suggestsFetchingStatus !== TwoGisSuggestFetchingStatus.INITIAL) {
            return;
        }

        dispatch(startFetchingTwoGisSuggests());

        getSuggestsList({ q: inputValue, locale: 'ru_RU', latitude, longitude })
            .then((items) => {
                dispatch(finishFetchingTwoGisSuggests(items));
            })
            .catch(() => {
                dispatch(finishFetchingTwoGisSuggests([]));
            });
    }, [dispatch, inputValue, latitude, longitude, suggestsFetchingStatus]);
};

export const useHandleCantFindAddressClick = () => {
    const dispatch = useDispatch();

    return React.useCallback(() => {
        dispatch(goBack());
    }, [dispatch]);
};
