import React from 'react';
import { replace } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { closeAppAction } from 'store/actions/app';

function useAddressString() {
    const address = useSelector(({ userModel }) => userModel.address);
    const city = useSelector(({ userModel }) => userModel.city);

    return React.useMemo(() => [city, address].join(', '), [address, city]);
}

function useHandleChangeButtonClick() {
    const dispatch = useDispatch();

    return React.useCallback(() => {
        dispatch(replace(ROUTES.ADDRESS_SIMPLE_GUIDE));
    }, [dispatch]);
}

function useHandleCloseButtonClick() {
    const dispatch = useDispatch();

    return React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);
}

export function useNoAreaPage() {
    const addressString = useAddressString();
    const handleChangeButtonClick = useHandleChangeButtonClick();
    const handleCloseButtonClick = useHandleCloseButtonClick();

    return { addressString, handleChangeButtonClick, handleCloseButtonClick };
}
