import React from 'react';

export interface KeyboardExpandHandler {
    (isKeyboardExpanded: boolean, keyboardExpandedCount: number): void;
}
export const useKeyboardExpanded = (handleKeyboardExpand: KeyboardExpandHandler) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [_, setKeyboardExpandedCount] = React.useState(0);

    React.useLayoutEffect(() => {
        const handleWindowResize = () => {
            setKeyboardExpandedCount((currentKeyboardExpandedValue) => {
                const nextKeyboardExpandedValue = currentKeyboardExpandedValue + 1;
                const isKeyboardExpanded = nextKeyboardExpandedValue % 2 !== 0;

                handleKeyboardExpand(isKeyboardExpanded, nextKeyboardExpandedValue);

                return nextKeyboardExpandedValue;
            });
        };

        window.addEventListener('resize', handleWindowResize);

        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, [setKeyboardExpandedCount, handleKeyboardExpand]);
};
