import React from 'react';
import { ButtonSize, ButtonViews } from 'components/Button/Button';
import {
    LocationButtonPortal,
    LocationMapBackgroundPortal,
    LocationPagePortal,
    LocationTextPortal,
    LocationTitlePortal,
} from 'components/LocationPage/LocationPage@portal';

import { NoAreaPin } from './NoArea';
import { useNoAreaPage } from './NoArea.hooks';

export const NoAreaPortal: React.FC = () => {
    const { addressString, handleChangeButtonClick, handleCloseButtonClick } = useNoAreaPage();

    const buttons = React.useMemo(
        () => (
            <>
                <LocationButtonPortal
                    size={ButtonSize.s}
                    view={ButtonViews.primary}
                    shouldFocusOnMount
                    onClick={handleChangeButtonClick}
                >
                    Ввести другой
                </LocationButtonPortal>
                <LocationButtonPortal onClick={handleCloseButtonClick} view={ButtonViews.secondary}>
                    Не сейчас
                </LocationButtonPortal>
            </>
        ),
        [handleChangeButtonClick, handleCloseButtonClick],
    );

    const background = React.useMemo(() => <LocationMapBackgroundPortal />, []);

    return (
        <LocationPagePortal buttons={buttons} background={background}>
            <NoAreaPin />
            <LocationTitlePortal>{addressString}</LocationTitlePortal>
            <LocationTextPortal>К сожалению, пока не доставляем по этому адресу</LocationTextPortal>
        </LocationPagePortal>
    );
};
