import React from 'react';
import styled from 'styled-components';
import { Footnote1, Headline4, Caption } from '@sberdevices/plasma-styles';
import { primary, secondary } from '@sberdevices/plasma-tokens';

import { useHeaderTextContent } from '../Header.hooks';
import { ReactComponent as LogoSvg } from '../Header.assets/logo.svg';

const logoSizePx = 72;

const StyledLogo = styled(LogoSvg)`
    width: ${logoSizePx}px;
    height: ${logoSizePx}px;
    border-radius: 16px;
`;

const StyledTitle = styled(Headline4)`
    color: ${primary};
`;

const StyledSubtitle = styled(Footnote1)`
    color: ${secondary};
`;

const StyledCaption = styled(Caption)`
    margin-left: 24px;

    display: flex;

    flex-direction: column;
`;

export const HeaderTitle = () => {
    const { title, subtitle } = useHeaderTextContent();
    const hasSubtitle = Boolean(subtitle);

    return (
        <>
            <StyledLogo />
            <StyledCaption>
                <StyledTitle>{title}</StyledTitle>

                {hasSubtitle ? <StyledSubtitle>{subtitle}</StyledSubtitle> : null}
            </StyledCaption>
        </>
    );
};
