import React from 'react';
import { useSelector } from 'hooks/useSelector';
import {
    CartInfoPinSberbox,
    CartInfoContainerSberbox,
    CartPersonalSberbox,
    CartAddressSberbox,
} from 'components/Cart/Info/CartInfo@sberbox';

export const StatusDetailsInfoSberbox: React.FC = () => {
    const { address } = useSelector(({ userData }) => userData);
    const { phoneNumber, personalAddress } = useSelector(({ orderStatusDetailsPage }) => orderStatusDetailsPage);

    return (
        <CartInfoContainerSberbox>
            <CartInfoPinSberbox />
            <CartAddressSberbox>{address}</CartAddressSberbox>
            <CartPersonalSberbox address={personalAddress} phone={phoneNumber} />
        </CartInfoContainerSberbox>
    );
};
