import React from 'react';
import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ErrorType } from 'store/constants/error';
import { AppAction } from 'store/constants';
import { ButtonProps, ButtonViews } from 'components/Button/Button';
import { closeAppAction } from 'store/actions/app';
import { ROUTES } from 'store/constants/router';
import { goBackToStartAndReplaceThunk } from 'store/thunks/native';

interface ErrorPageButtonData {
    shouldFocusOnMount?: boolean;
    text: string;
    view?: ButtonViews;
    action: () => AppAction;
}

interface ErrorPageProps {
    title?: string;
    text?: string;
    retryButton?: ErrorPageButtonData;
    closeButton?: ErrorPageButtonData;
}

export type ErrorPropsMap = Record<ErrorType, ErrorPageProps>;

export const mapErrorTypeToPropsCommon: ErrorPropsMap = {
    [ErrorType.NONE]: {},
    [ErrorType.STATUS]: {
        retryButton: {
            shouldFocusOnMount: true,
            text: 'Повторить',
            action: () => goBackToStartAndReplaceThunk(ROUTES.STATUS_SUMMARY),
        },
        closeButton: {
            text: 'Не сейчас',
            action: closeAppAction,
        },
    },
    [ErrorType.ORDER_PARTNER]: {
        retryButton: {
            shouldFocusOnMount: true,
            text: 'Повторить',
            action: goBack,
        },
        closeButton: {
            text: 'Не сейчас',
            action: closeAppAction,
        },
    },
    [ErrorType.ORDER_TIMING]: {
        closeButton: {
            shouldFocusOnMount: true,
            text: 'Очень жаль',
            action: closeAppAction,
        },
    },
};

interface UseErrorPageParams {
    mapErrorTypeToProps: ErrorPropsMap;
    renderButton: (props: ButtonProps) => React.ReactNode;
}

export const useErrorPage = ({ mapErrorTypeToProps, renderButton }: UseErrorPageParams) => {
    const errorType = useSelector(({ error }) => error.type);

    const { title, text, retryButton, closeButton } = React.useMemo(() => mapErrorTypeToProps[errorType], [
        errorType,
        mapErrorTypeToProps,
    ]);

    const dispatch = useDispatch();

    const handleRetryClick = React.useCallback(() => {
        if (retryButton) {
            dispatch(retryButton.action());
        }
    }, [dispatch, retryButton]);

    const retryButtonProps: ButtonProps | undefined = retryButton && {
        ...retryButton,
        onClick: handleRetryClick,
    };

    const handleCloseClick = React.useCallback(() => {
        if (closeButton) {
            dispatch(closeButton.action());
        }
    }, [dispatch, closeButton]);

    const closeButtonProps: ButtonProps | undefined = closeButton && {
        ...closeButton,
        onClick: handleCloseClick,
    };

    const buttons = React.useMemo(
        () => (
            <>
                {retryButtonProps && renderButton(retryButtonProps)}
                {closeButtonProps && renderButton(closeButtonProps)}
            </>
        ),
        [renderButton, retryButtonProps, closeButtonProps],
    );

    return {
        title,
        text,
        buttons,
    };
};
