import React, { useRef, useState, useLayoutEffect } from 'react';
import styled from 'styled-components';
import * as Sentry from '@sentry/react';
import DG from '2gis-maps';
import { useDispatch } from 'hooks/useDispatch';
import { Pin, HappyFaceIcon } from 'components/Pin/Pin@touch';
import { getGeolocation } from 'tools/geolocation';
import { memoizeResolvedPromise } from 'tools/memoize';
import { isErrorResponse } from 'tools/twoGis/api/common';
import { getGeocode } from 'tools/twoGis/api/items';
import { DGBuildingSuggest } from 'tools/twoGis/api/suggest';
import { revertUserChanges } from 'store/actions/app';
import { addressSelectSuggestAction } from 'store/actions/addressMap';
import { critical } from '@sberdevices/plasma-tokens';

let map: DG.DGMap;

const StyledAddressMapTouch = styled.div`
    position: relative;

    display: flex;
    flex-direction: column;

    width: 100%;
    height: 100%;
`;

const StyledMap = styled.div`
    height: calc(100% + 65px);
    width: 100%;
    margin-top: -65px;
    filter: grayscale(71%) brightness(47%) hue-rotate(163deg) saturate(302%); // ToDo: найти более изящное решение для стилизации карты
`;

const StyledAddress = styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    width: 311px;

    color: white;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.025em;
    margin-top: -14px;
    pointer-events: none;

    transform: translateX(-50%);
`;

const PinTouch = styled(Pin)`
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: -60px;
`;

const memoizedGetItemsGeocode = memoizeResolvedPromise(getGeocode);

export const AddressMapTouch: React.FC = () => {
    const dispatch = useDispatch();
    const [address, setAddress] = useState<DGBuildingSuggest | null>(null);
    const mapRef = useRef<HTMLDivElement>(null);
    const onLoad = React.useCallback(() => {
        if (mapRef.current instanceof HTMLDivElement) {
            mapRef.current.focus();
        }
    }, [mapRef]);

    const onMove = React.useCallback(
        (e) => {
            const { x, y } = e.target.getSize();
            const { lng, lat } = map.containerPointToLatLng({ x: x / 2, y: y / 2 });

            memoizedGetItemsGeocode({ point: [lng, lat].join(',') })
                .then((response) => {
                    if (isErrorResponse(response)) {
                        if (response.meta.code === 404) {
                            return [];
                        }

                        throw new Error(`Error: 2GIS Get geocode: ${response.meta.error.message}`);
                    }

                    return response.result.items;
                })
                .then(([firstItem]) => {
                    if (firstItem) {
                        setAddress(firstItem);
                        dispatch(addressSelectSuggestAction(firstItem));
                    } else {
                        setAddress(null);
                        dispatch(revertUserChanges());
                    }
                })
                .catch((error) => {
                    if (process.env.REACT_APP_ENVIRONMENT === 'production') {
                        Sentry.captureException(error);
                    } else {
                        // eslint-disable-next-line no-console
                        console.error(error);
                    }

                    return [];
                });
        },
        [dispatch],
    );

    useLayoutEffect(() => {
        getGeolocation()
            // Если не удалось определить местоположение то выставляем Батайский проезд 27
            .catch(() => ({ coords: { latitude: 55.644242, longitude: 37.72276 } }))
            .then(({ coords: { latitude, longitude } }) => {
                map = DG.map('map', {
                    center: [latitude, longitude],
                    zoom: 18,
                    zoomControl: false,
                    fullscreenControl: false,
                });

                map.addEventListener('moveend', onMove);
                map.addEventListener('load', onLoad);
            });

        return () => {
            map.removeEventListener('moveend', onMove);
            map.removeEventListener('load', onLoad);
        };
    }, [onMove, onLoad]);

    return (
        <StyledAddressMapTouch>
            <StyledMap id="map" ref={mapRef} />
            <PinTouch pinColor={critical}>
                <HappyFaceIcon />
            </PinTouch>
            <StyledAddress>{address?.address_name}</StyledAddress>
        </StyledAddressMapTouch>
    );
};
