import React from 'react';
import styled, { css } from 'styled-components';
import { Button2 } from '@sberdevices/plasma-styles';
import { buttonSecondary, primary } from '@sberdevices/plasma-tokens';
import { transition } from 'style/const/transition';
import { icon } from 'style/const/icon';
import { focusBorderMixin } from 'style/mixin/focus';

const containerBorderRadiusPx = 24;

interface StyledHeaderButtonProps {
    isVisible?: boolean;
}

const StyledHeaderButton = styled.button<StyledHeaderButtonProps>`
    position: relative;

    display: flex;
    align-items: center;

    margin: 0 0 0 24px;

    box-sizing: border-box;
    height: 80px;
    padding: 16px 36px;
    border: none;
    border-radius: ${containerBorderRadiusPx}px;

    background: transparent;

    font-family: inherit;

    transition: background-color ${transition.default};
    will-change: background-color;

    ${focusBorderMixin({ borderRadius: containerBorderRadiusPx })}

    ${({ isVisible }) =>
        !isVisible &&
        css`
            display: none;
        `}

    &:focus {
        background: ${buttonSecondary};
    }

    &:first-of-type {
        margin-left: 0;
    }
`;

const textHeightPx = icon.m.sizePx;

const StyledText = styled(Button2)`
    margin-left: 8px;

    height: ${textHeightPx}px;
    padding: 0 4px;

    line-height: ${textHeightPx}px;

    color: ${primary};

    opacity: 0.88;
`;

interface HeaderButtonProps {
    forwardRef?: React.RefObject<HTMLButtonElement>;
    text: string;
    isVisible?: boolean;
    onClick?: () => void;
}

export const HeaderButton: React.FC<HeaderButtonProps> = ({
    forwardRef,
    text,
    onClick: handleClick,
    children,
    ...props
}) => {
    return (
        <StyledHeaderButton ref={forwardRef} tabIndex={0} onClick={handleClick} type="button" {...props}>
            {children}
            <StyledText>{text}</StyledText>
        </StyledHeaderButton>
    );
};
