import { createGlobalStyle } from 'styled-components';
import React from 'react';
import { ThemeDarkEva, ThemeDarkJoy, ThemeDarkSber } from '@sberdevices/plasma-styles';
import { darkSber } from '@sberdevices/plasma-tokens/themes/darkSber';
import { useSelector } from 'hooks/useSelector';
import { Character } from 'tools/assistant';
import { selectCharacter } from 'store/selectors';

export const DefaultTheme = createGlobalStyle`
    ${darkSber}

    /* stylelint-disable-next-line selector-nested-pattern */
    :root {
        --plasma-colors-background: transparent;
        --plasma-colors-buttonAccent: #000;
        --plasma-colors-buttonFocused: #080808;
        --plasma-colors-gradient: transparent;
        color: #fff;
        background-color: transparent;
    }
`;

type ThemeType = typeof ThemeDarkEva | typeof ThemeDarkJoy | typeof ThemeDarkSber;

const characterIdToThemeMap: Record<Character, ThemeType> = {
    eva: ThemeDarkEva,
    joy: ThemeDarkJoy,
    sber: ThemeDarkSber,
};

export const Theme: React.FC = () => {
    const characterId = useSelector(selectCharacter);

    const CharacterTheme = characterIdToThemeMap[characterId] || DefaultTheme;

    return <CharacterTheme />;
};
