import {
    AssistantCharacterCommand,
    AssistantCommands,
    AssistantEvents,
    AssistantSmartAppCommand,
    createAssistant,
    createAssistantDev,
    createRecordPlayer,
} from '@sberdevices/assistant-client';
import { showCategoriesRecord } from 'records/showCategories';
import { AppState } from 'store/constants';
import { SmartAppCommand } from 'store/constants/native';
import { AssistantSdkServerAction } from 'store/constants/server';
import { DeviceFamily, deviceFamily, isPROM, queryParams } from 'tools/environment';

type ExtractFirstParam<T> = T extends (firstParam: infer U) => void ? U : never;
type ExtractSecondParam<T> = T extends (firstParam: infer A, secondParam: infer B) => void ? B : never;
type ExtractParams<T> = T extends (...args: infer A) => void ? A[number] : never;

type CreateAssistantDev = typeof createAssistantDev;
type CreateAssistantDevParams = ExtractFirstParam<CreateAssistantDev>;
type AssistantSdkInstance = ReturnType<CreateAssistantDev>;

export interface AppInfo {
    app_info: {
        projectId: string; // '0da9a4a0-cb52-4490-afce-4f535d9d1eb5'
        applicationId: string; // "a3940aa5-6efa-4422-b6fb-6df2bf1a1c6e"
        systemName: string; // "popcorn"
        frontendEndpoint: string; // "https://avstarikovich.github.io/samokat"
        frontendType: string; // "WEB_APP"
        frontendStateId: string; // "0da9a4a0-cb52-4490-...0-9154-a4e9c6429011"
    };
    device_id: string; // "GZ200301320000141"
    platform: string; // "ANDROID"
    sdk_version: string; // "1.0.0.0"
}

export interface AssistantAppContextCommand {
    type: 'app_context';
    app_context: AppInfo;
}

export type Character = AssistantCharacterCommand['character']['id'];

export type RealAssistantSdkOnData = AssistantEvents['data'];

export type AssistantSdkNonSmartAppCommand =
    | Exclude<AssistantCommands, AssistantSmartAppCommand>
    | AssistantAppContextCommand;
export type AssistantSdkCommand = SmartAppCommand | AssistantSdkNonSmartAppCommand;

export type AssistantSdkGetState = CreateAssistantDevParams['getState'];
export type AssistantSdkGetRecoveryState = Exclude<CreateAssistantDevParams['getRecoveryState'], undefined>;
export type AssistantSdkOnData = (command: AssistantSdkCommand) => void;
export type AssistantSdkOnStart = AssistantEvents['start'];

const assistantDevEndpoint = 'wss://vpstest2.online.sberbank.ru:443/vpsdemo2/';

const initPhrase = queryParams.initPhrase || 'Запусти самокат';

const recordToPlay = queryParams.record ? showCategoriesRecord : undefined;

const noop = () => ({});

if (!isPROM) {
    // eslint-disable-next-line no-underscore-dangle
    window.__inbox = window.__inbox || [];
    // eslint-disable-next-line no-underscore-dangle
    window.__outbox = window.__outbox || [];
}

export const isAssistantDevMode = !isPROM && queryParams.debug;

const assistant: AssistantSdkInstance = isAssistantDevMode
    ? createAssistantDev({
          getState: noop,
          getRecoveryState: noop,
          url: assistantDevEndpoint,
          surface: 'SBERBOX',
          userChannel: queryParams.userChannel || 'B2C',
          enableRecord: true,
          // initPhrase: recordToPlay ? '' : initPhrase,
          initPhrase,
      })
    : createAssistant({
          getState: noop,
          getRecoveryState: noop,
      });

// if (isAssistantDevMode && recordToPlay) {
// const player = createRecordPlayer(recordToPlay);

// setTimeout(() => {
//     player.play();
// });
// }

export const close: AssistantSdkInstance['close'] = () => {
    if (!isPROM) {
        // eslint-disable-next-line no-console
        console.log('call close native method');
    }

    assistant.close();
};

export const sendData = (serverAction: AssistantSdkServerAction) => {
    if (!isPROM) {
        // eslint-disable-next-line no-console
        console.log(`sendData: ${JSON.stringify(serverAction)}`);
    }

    assistant.sendData({ action: serverAction });
};

/**
 * TODO: прикрутить валидацию recoveryStat'a
 * @see https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-200
 */
export const getRecoveryState = () => {
    const recoveryState = assistant.getRecoveryState();

    /**
     * На мобилках recoveryState уже пробрасывается, но мы его еще полноценно не поддержали,
     * поэтому он пока что выключен.
     */
    if (deviceFamily === DeviceFamily.MOBILE) {
        return undefined;
    }

    return recoveryState as AppState;
};

export const getAppInitialData = (): AssistantSdkCommand[] => {
    const appInitialData = assistant.getInitialData();

    return appInitialData as AssistantSdkCommand[];
};

const player = createRecordPlayer(recordToPlay);
let isFirstDataReceived = false;

export const setOnData = (onData: AssistantSdkOnData) => {
    const onDataWrapper: AssistantSdkOnData = (command) => {
        if (isAssistantDevMode && recordToPlay) {
            if (!isFirstDataReceived) {
                isFirstDataReceived = true;

                setTimeout(() => {
                    player.play();
                });
            }
        }

        if (!isPROM) {
            // eslint-disable-next-line no-underscore-dangle
            window.__inbox.push(command);
        }

        if (!isPROM) {
            // eslint-disable-next-line no-console
            console.log(`server command: ${JSON.stringify(command)}`);
        }

        onData(command);
    };

    assistant.on('data', onDataWrapper as RealAssistantSdkOnData);
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export const setOnStart = (onStart: AssistantSdkOnStart) => assistant.on('start', onStart);

export const setGetState = (getState: AssistantSdkGetState) => {
    const getStateWrapper = () => {
        const state = getState();

        if (!isPROM) {
            // eslint-disable-next-line no-console
            console.log('invoke request state callback', JSON.stringify(state));
        }

        return state;
    };

    assistant.setGetState(getStateWrapper);
};

export const setGetRecoveryState = (getRecoveryStateCallback: AssistantSdkGetRecoveryState) => {
    const getRecoveryStateWrapper: AssistantSdkGetRecoveryState = () => {
        const recoveryState = getRecoveryStateCallback();

        if (!isPROM) {
            // eslint-disable-next-line no-console
            console.log('invoke request recovery state callback', JSON.stringify(recoveryState, null, 2));
        }

        return recoveryState;
    };

    assistant.setGetRecoveryState(getRecoveryStateWrapper);
};

if (!isAssistantDevMode) {
    /**
     * setTimeout нужен для того чтобы нормально отрабатывали "навигационные" команды.
     * проблема в том, что у нас есть connected-react-router который внутри себя
     * содержит завязку на react, history и redux. Подписка connected-react-router'a
     * происходит в момент рендера компоненте ConnectedRouter.
     * Т.е. пока верстка не промаунтится - навигация будет работать некорректно.
     * Как раз для того чтобы все завелось мы и вынесим обработку appInitialData
     * из треда в отредельную таску, для того чтобы сперва замаунтились компоненты,
     * а уже потом пошла инициализация.
     */
    setTimeout(() => {
        if (Array.isArray(window.appInitialData)) {
            const appInitialData = getAppInitialData();

            appInitialData.forEach((command) => {
                window.AssistantClient.onData(command);
            });
        }
    });
}
