import { AppThunkAction } from 'store/constants';
import { itemDetailsSelectItemAction } from 'store/actions/itemDetailsPage';
import { ROUTES } from 'store/constants/router';
import { push } from 'connected-react-router';

export const itemDetailsItemSelectThunk = (payload: string): AppThunkAction => (dispatch) => {
    dispatch(itemDetailsSelectItemAction(payload));
    dispatch(push(ROUTES.ITEM_DETAILS));
};
