import React from 'react';
import {
    DialogPagePortal,
    DialogTitlePortal,
    DialogButtonPortal,
    DialogTextPortal,
} from 'components/DialogPage/DialogPage@portal';
import { MultilineText } from 'components/MultilineText/MultilineText';

import { useRenderErrorPage } from './RenderError.hooks';

export const RenderErrorPortal: React.FC = () => {
    const { handleCloseApp, handleGoBack } = useRenderErrorPage();

    const buttons = React.useMemo(
        () => (
            <>
                <DialogButtonPortal shouldFocusOnMount onClick={handleGoBack}>
                    Вернуться назад
                </DialogButtonPortal>
                <DialogButtonPortal onClick={handleCloseApp}>Очень жаль</DialogButtonPortal>
            </>
        ),
        [handleCloseApp, handleGoBack],
    );

    return (
        <DialogPagePortal buttons={buttons}>
            <DialogTitlePortal>
                <MultilineText text="Что-то пошло не так" />
            </DialogTitlePortal>
            <DialogTextPortal>
                <MultilineText text="Пожалуйста, попробуйте ещё раз" />
            </DialogTextPortal>
        </DialogPagePortal>
    );
};
