import React from 'react';
import styled from 'styled-components';
import { Headline2 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { preventScroll } from 'tools/scroll';

export const CartListContainerSberbox = styled.div`
    position: relative;

    height: 792px;
    width: 832px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const StyledScrollerContainer = styled.div`
    --spatial-navigation-contain: contain;

    position: relative;

    margin-bottom: 56px;

    max-height: 600px;
    overflow: hidden;
`;

const StyledScrollerWrapper = styled.div`
    overflow-x: hidden;

    will-change: transform;
    transition: transform 0.5s ease-in-out;
`;

interface CartListScrollerProps {
    topGradientVisible: boolean;
    bottomGradientVisible: boolean;
    wrapperYShift: number;
}

export const CartListScrollerSberbox: React.FC<CartListScrollerProps> = ({
    children,
    topGradientVisible,
    bottomGradientVisible,
    wrapperYShift,
}) => {
    const wrapperStyle = React.useMemo(
        () => ({
            transform: `translateY(${-1 * wrapperYShift}px)`,
        }),
        [wrapperYShift],
    );

    return (
        <GradientFade sizePx={160} kind="top" isVisible={topGradientVisible}>
            <GradientFade sizePx={160} kind="bottom" isVisible={bottomGradientVisible}>
                <StyledScrollerContainer onScroll={preventScroll}>
                    <StyledScrollerWrapper style={wrapperStyle}>{children}</StyledScrollerWrapper>
                </StyledScrollerContainer>
            </GradientFade>
        </GradientFade>
    );
};

export const CartTotalPriceSberbox = styled(Headline2)`
    padding-left: 16px;

    color: ${primary};
`;
