import React from 'react';
import styled from 'styled-components';
import { FullscreenDecoration } from 'components/FullscreenDecoration/FullscreenDecoration';
import { layoutHeightVar } from 'components/GlobalLayoutVars/GlobalLayoutVars';

import { ReactComponent as CirclesSvg } from '../Error.assets/circles.svg';
import { ReactComponent as WarningSvg } from '../Error.assets/warning.svg';

const varNamePrefix = '--error-decoration';

export const errorDecorationIconSizeVar = `${varNamePrefix}-icon-size`;
export const errorDecorationContainerSizeVar = `${varNamePrefix}-container-size`;

const Container = styled.div`
    position: absolute;
    top: 50%;
    margin-top: calc(-0.5 * var(${errorDecorationContainerSizeVar}));
    right: calc(-0.5 * (var(${errorDecorationContainerSizeVar}) - var(${layoutHeightVar})));

    width: var(${errorDecorationContainerSizeVar});
    height: var(${errorDecorationContainerSizeVar});

    display: flex;
    justify-content: center;
    align-items: center;
`;

const Circles = styled(CirclesSvg)`
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;

    width: 100%;
    height: 100%;
`;

const WarningIcon = styled(WarningSvg)`
    position: relative;
    z-index: 2;

    width: var(${errorDecorationIconSizeVar});
    height: var(${errorDecorationIconSizeVar});
`;

export const ErrorPageDecorationCommon: React.FC = (props) => (
    <FullscreenDecoration {...props}>
        <Container>
            <Circles />
            <WarningIcon />
        </Container>
    </FullscreenDecoration>
);
