import React from 'react';
import styled, { css } from 'styled-components';
import { Body2 } from '@sberdevices/plasma-styles';
import { buttonSecondary, critical, primary, warning } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { removeItemAction, addItemAction } from 'store/actions/native';
import { cartSelectItemAction } from 'store/actions/cartPage';
import { Button, ButtonViews } from 'components/Button/Button';
import { LabelProps } from 'components/Label/Label';
import {
    CartListItemRootPortal,
    CartListItemThumbPortal,
    CartListItemInfoPortal,
} from 'components/Cart/ListItem/CartListItem@portal';
import { icon } from 'style/const/icon';
import { transition } from 'style/const/transition';
import { ReactComponent as AddSvg } from 'assets/icons/add.svg';
import { ReactComponent as RemoveSvg } from 'assets/icons/remove.svg';

const StyledSelector = styled.div`
    box-sizing: border-box;
    padding: 8px;
    width: 216px;
    flex-shrink: 0;

    display: flex;
    justify-content: space-between;
    align-items: center;
`;

interface StyledSelectorValue {
    isWarning?: boolean;
}
const StyledSelectorValue = styled(Body2)<StyledSelectorValue>`
    color: ${({ isWarning }) => (isWarning ? warning : primary)};
`;

const selectorButtonSizePx = 64;
const selectorBorderRadiusPx = selectorButtonSizePx / 2;
const selectorIconSizePx = icon.s.sizePx;

interface StyledSelectorButtonProps {
    isRemoving?: boolean;
}

const StyledSelectorButton = styled.button<StyledSelectorButtonProps>`
    position: relative;

    height: ${selectorButtonSizePx}px;
    width: ${selectorButtonSizePx}px;
    border: none;
    border-radius: ${selectorBorderRadiusPx}px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: ${buttonSecondary};

    will-change: background-color, transform, opacity;
    transition: ${transition.default};

    ${({ isRemoving }) =>
        isRemoving &&
        css`
            transform: rotate(45deg);
            background: ${critical};
        `}

    &[disabled] {
        opacity: 0.3;
    }
`;

const StyledAddIcon = styled(AddSvg)`
    height: ${selectorIconSizePx}px;
    width: ${selectorIconSizePx}px;
`;

const StyledRemoveIcon = styled(RemoveSvg)`
    height: ${selectorIconSizePx}px;
    width: ${selectorIconSizePx}px;
`;

type ButtonProps = React.ButtonHTMLAttributes<HTMLButtonElement> & StyledSelectorButtonProps;

const LessButton: React.FC<ButtonProps> = ({ onClick, onFocus, isRemoving }) => (
    <StyledSelectorButton type="button" aria-label="less" onClick={onClick} onFocus={onFocus} isRemoving={isRemoving}>
        {isRemoving ? <StyledAddIcon /> : <StyledRemoveIcon />}
    </StyledSelectorButton>
);

const AddButton: React.FC<ButtonProps> = ({ onClick, onFocus }) => (
    <StyledSelectorButton type="button" aria-label="more" onClick={onClick} onFocus={onFocus}>
        <StyledAddIcon />
    </StyledSelectorButton>
);

const StyledDeleteButton = styled(Button)`
    padding: 20px 32px;
    flex-shrink: 0;
`;

const CartListItemContent = styled.div`
    display: flex;
    flex-wrap: wrap;
`;

const CartListItemControls = styled.div`
    display: flex;
    margin-left: -8px;
`;

export interface CartListItemProps {
    id: string;
    index: number;
}

export interface CartListItemViewProps {
    imageUrl: string;
    name: string;
    price: number;
    oldPrice?: number;
    count: number;
    isWantMoreThanHave?: boolean;
    disabled?: boolean;
    label?: LabelProps;
    isMoreButtonDisabled?: boolean;
}

export const CartListItemPortal: React.FC<CartListItemProps> = ({ index, id }) => {
    const {
        imageUrl,
        name,
        price,
        oldPrice,
        label,
        count,
        isWantMoreThanHave,
        disabled,
        isMoreButtonDisabled,
    } = useSelector(({ cartPage: { itemsMap } }) => itemsMap[id]);
    const dispatch = useDispatch();

    const handleLessClick = React.useCallback(() => {
        dispatch(
            removeItemAction({
                item: id,
                count: '1',
                shouldRemoveMention: !count,
            }),
        );
    }, [dispatch, id, count]);

    const handleMoreClick = React.useCallback(() => {
        dispatch(
            addItemAction({
                item: id,
                count: '1',
            }),
        );
    }, [dispatch, id]);

    const handleRemoveClick = React.useCallback(() => {
        dispatch(
            removeItemAction({
                item: id,
                count: String(Infinity),
                shouldRemoveMention: true,
            }),
        );
    }, [dispatch, id]);

    const handleFocus = React.useCallback(() => dispatch(cartSelectItemAction(id)), [dispatch, id]);

    return (
        <CartListItemRootPortal disabled={disabled}>
            <CartListItemThumbPortal imageUrl={imageUrl} index={index} />
            <CartListItemContent>
                <CartListItemInfoPortal
                    disabled={disabled}
                    label={label}
                    name={name}
                    price={price}
                    oldPrice={oldPrice}
                />
                <CartListItemControls>
                    {disabled ? (
                        <StyledDeleteButton
                            view={ButtonViews.secondary}
                            onFocus={handleFocus}
                            onClick={handleRemoveClick}
                        >
                            Удалить
                        </StyledDeleteButton>
                    ) : (
                        <StyledSelector>
                            <LessButton onFocus={handleFocus} onClick={handleLessClick} isRemoving={count === 0} />
                            <StyledSelectorValue isWarning={isWantMoreThanHave}>{count}</StyledSelectorValue>
                            <AddButton
                                disabled={isMoreButtonDisabled}
                                onFocus={handleFocus}
                                onClick={handleMoreClick}
                            />
                        </StyledSelector>
                    )}
                </CartListItemControls>
            </CartListItemContent>
        </CartListItemRootPortal>
    );
};
