import React from 'react';
import styled from 'styled-components';

import { ReactComponent as SadFaceSvg } from './Pin.assets/sad-face.svg';
import { ReactComponent as ShadowSvg } from './Pin.assets/shadow.svg';
import { ReactComponent as HappyFaceSvg } from './Pin.assets/happy-face.svg';

const badgeHeightPx = 43;

export const SadFaceIcon = styled(SadFaceSvg)`
    display: block;
    height: 42px;
    flex: 0 0 42px;
    margin: 0 -10px;
    max-width: 42px;
`;

export const HappyFaceIcon = styled(HappyFaceSvg)`
    display: block;
    height: 42px;
    flex: 0 0 42px;
    margin: 0 -10px;
    max-width: 42px;
`;

const StyledBody = styled.div`
    position: relative;

    height: ${badgeHeightPx}px;
    min-width: ${badgeHeightPx}px;
    padding: 6px;
    border-radius: ${badgeHeightPx / 2}px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;
`;

const kernelWidthPx = 2;
const kernelBorderRadiusPx = kernelWidthPx / 2;

const StyledKernelTop = styled.div`
    position: relative;
    display: flex;

    height: 10px;
    width: ${kernelWidthPx}px;

    border-radius: ${kernelBorderRadiusPx}px;

    margin-bottom: -4px;
    margin-top: -2px;
`;

const StyledKernelBottom = styled.div`
    height: 15px;
    position: relative;
    width: ${kernelWidthPx}px;

    border-radius: ${kernelBorderRadiusPx}px;
`;

const StyledShadow = styled(ShadowSvg)`
    display: block;
    margin-top: -3px;

    width: 37px;
    height: 8px;
`;

const StyledPin = styled.div<PinInterface>`
    display: flex;
    flex-direction: column;
    align-items: center;

    & ${StyledBody}, ${StyledKernelBottom}, ${StyledKernelTop} {
        background-color: ${({ pinColor }) => pinColor};
    }
`;

export interface PinInterface {
    className?: string;
    pinColor: string;
}
export const Pin: React.FC<PinInterface> = ({ className, pinColor, children }) => {
    return (
        <StyledPin pinColor={pinColor} className={className}>
            <StyledBody>{children}</StyledBody>
            <StyledKernelTop />
            <StyledKernelBottom />
            <StyledShadow />
        </StyledPin>
    );
};
