import React from 'react';
import styled from 'styled-components';
import { OrderStatusCategory } from 'store/constants/orderStatus';
import { FullscreenDecoration } from 'components/FullscreenDecoration/FullscreenDecoration';

import { ReactComponent as PendingSvg } from '../StatusSummary.assets/pending.svg';
import { ReactComponent as DeliveringSvg } from '../StatusSummary.assets/delivering.svg';
import { ReactComponent as SuccessSvg } from '../StatusSummary.assets/success.svg';
import { ReactComponent as FailedSvg } from '../StatusSummary.assets/failed.svg';

const styledSVG = (svg: React.FunctionComponent<React.SVGProps<SVGSVGElement>>) => styled(svg)`
    width: 100%;
    height: 100%;
`;

const mapCategoryToBackground: Record<OrderStatusCategory, React.FunctionComponent<React.SVGProps<SVGSVGElement>>> = {
    [OrderStatusCategory.PENDING]: styledSVG(PendingSvg),
    [OrderStatusCategory.DELIVERING]: styledSVG(DeliveringSvg),
    [OrderStatusCategory.SUCCESS]: styledSVG(SuccessSvg),
    [OrderStatusCategory.FAILED]: styledSVG(FailedSvg),
};

interface StatusSummaryDecorationProps {
    orderStatusCategory: OrderStatusCategory;
}
export const StatusSummaryDecoration: React.FC<StatusSummaryDecorationProps> = React.memo(({ orderStatusCategory }) => {
    const BackgroundSvg = mapCategoryToBackground[orderStatusCategory];

    return (
        <FullscreenDecoration>
            <BackgroundSvg />
        </FullscreenDecoration>
    );
});
