import React from 'react';
import styled, { AnyStyledComponent } from 'styled-components';
import { black, blackTertiary, primary } from '@sberdevices/plasma-tokens';

import bgImage from './LocationPage.assets/bg.jpg';

export const styledTitle = (base: AnyStyledComponent) => styled(base)`
    text-align: center;

    color: ${primary};
`;

export const styledText = (base: AnyStyledComponent) => styled(base)`
    margin-top: 12px;

    text-align: center;

    color: ${primary};
`;

export const LocationBackgroundCommon = styled.div`
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;

    mask: linear-gradient(0.3deg, ${blackTertiary} 0.26%, ${black} 53.11%, ${blackTertiary} 99.74%);
    background-image: url('${bgImage}');

    opacity: 0.6;
`;

const StyledContainer = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;

    display: flex;

    justify-content: center;
    align-items: center;
    flex-direction: column;
`;

export const StyledButtonContainerCommon = styled.div`
    display: flex;
    justify-content: center;
`;

export interface LocationPageProps {
    buttons?: React.ReactNode;
    background?: React.ReactNode;
}

interface LocationPageCommonProps extends LocationPageProps {
    background?: React.ReactNode;
    renderButtonContainer(): React.ReactElement;
}

export const LocationPageCommon: React.FC<LocationPageCommonProps> = ({
    children,
    background,
    buttons,
    renderButtonContainer,
}) => {
    const buttonContainer = React.useMemo(() => {
        if (!buttons) {
            return null;
        }

        return renderButtonContainer();
    }, [buttons, renderButtonContainer]);

    return (
        <>
            <StyledContainer>
                {children}
                {buttonContainer}
            </StyledContainer>
            {background}
        </>
    );
};
