import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { formatPhone } from 'tools/phoneNumber';

export const useStatusReportPage = () => {
    const reportPhone = useSelector((state) => state.orderStatusData.reportPhone);

    const phoneNumber = React.useMemo(() => formatPhone(reportPhone), [reportPhone]);

    return {
        phoneNumber,
    };
};
