import { CarouselProvider } from 'pure-react-carousel';
import React from 'react';
import styled from 'styled-components';
import 'pure-react-carousel/dist/react-carousel.es.css';

import { useCarouselActiveIndex, useCategoriesCount } from '../Categories.hooks';
import { activeCardGap, activeCardSize } from '../ListItem/CategoriesListItem';
import { CategoriesSlider } from '../Slider/CategoriesSlider';

const carouselItemSize = activeCardSize + activeCardGap * 2;
const carouselVisibleSlidesCount = 5;

const StyledWrapper = styled.div`
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: start;

    width: 100%;
    height: ${activeCardSize}px;
    padding-top: 90px;
`;

export const CategoriesList: React.FC = () => {
    const categoriesCount = useCategoriesCount();
    const activeIndex = useCarouselActiveIndex();

    const carousel = React.useMemo(
        () => (
            <CarouselProvider
                currentSlide={activeIndex}
                naturalSlideWidth={carouselItemSize}
                naturalSlideHeight={carouselItemSize}
                totalSlides={categoriesCount}
                visibleSlides={carouselVisibleSlidesCount}
                lockOnWindowScroll
                infinite
            >
                <CategoriesSlider />
            </CarouselProvider>
        ),
        [categoriesCount, activeIndex],
    );

    return <StyledWrapper>{carousel}</StyledWrapper>;
};
