import React from 'react';
import styled from 'styled-components';
import { Body2 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { icon } from 'style/const/icon';
import { layout } from 'style/const/layout@touch';
import { transition } from 'style/const/transition';

import { HeaderAddressTouch } from '../HeaderAddress/HeaderAddress@touch';
import { HeaderCartTouch } from '../HeaderCart/HeaderCart@touch';

import { ReactComponent as ChevronLeftSvg } from './Header.assets/chevron-left.svg';
import { ReactComponent as LogoSvg } from './Header.assets/logo.svg';
import { useHeaderTextContent, useHeaderVisibilityFlags } from './Header.hooks';
import {
    routeToVisibilityMapCommon,
    routesToAddressVisibilityMapCommon,
    routesToCartVisibilityMapCommon,
} from './Header';

interface StyledHeaderProps {
    isVisible: boolean;
}

const StyledHeader = styled.div<StyledHeaderProps>`
    --spatial-navigation-contain: contain;

    position: relative;

    height: ${layout.headerHeight}px;

    box-sizing: border-box;
    padding: 14px 13px 9px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};

    z-index: 2;
`;

const backButtonIconSizePx = icon.xs.sizePx;

const StyledBackButton = styled.span`
    width: 32px;
    height: 32px;
    margin-right: 9px;

    display: flex;
    align-items: center;
    justify-content: center;
`;

const StyledChevronLeft = styled(ChevronLeftSvg)`
    width: ${backButtonIconSizePx}px;
    height: ${backButtonIconSizePx}px;
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const StyledLogo = styled(LogoSvg)`
    margin-left: 3px;

    width: 28px;
    height: 28px;
    border-radius: 8px;
`;

const StyledTitle = styled(Body2)`
    margin-left: 10px;

    display: inline-block;

    color: ${primary};
`;

// eslint-disable-next-line  @typescript-eslint/no-unused-vars
export const HeaderTouch: React.FC = ({ children, ...props }) => {
    const { title } = useHeaderTextContent();
    const isBackButtonVisible = Boolean(window.history.length);
    const { isHeaderVisible: isVisible } = useHeaderVisibilityFlags({
        routesToCartVisibilityMap: routesToCartVisibilityMapCommon,
        routesToAddressVisibilityMap: routesToAddressVisibilityMapCommon,
        routeToVisibilityMap: routeToVisibilityMapCommon,
    });

    return (
        <StyledHeader isVisible={isVisible} {...props}>
            <StyledSection>
                {isBackButtonVisible && (
                    <StyledBackButton>
                        <StyledChevronLeft />
                    </StyledBackButton>
                )}
                <StyledLogo />
                <StyledTitle>{title}</StyledTitle>
            </StyledSection>
            <StyledSection>
                <HeaderAddressTouch />
                <HeaderCartTouch />
            </StyledSection>
        </StyledHeader>
    );
};
