import { useSelector } from 'hooks/useSelector';
import { selectPathname } from 'store/selectors';
import { AppState } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { OrderStatus } from 'store/constants/orderStatus';
import { splitByThousands } from 'tools/split-by-thousands';

const getOrderStatusTitle = (state: AppState): string =>
    state.orderStatusData.orderId ? `Заказ #${state.orderStatusData.orderId}` : 'Статус заказа';

const routeToTitleMap: Record<string, (state: AppState) => string> = {
    [ROUTES.CATALOG]: () => 'Самокат',
    [ROUTES.CATEGORIES]: () => 'Самокат',
    [ROUTES.CART]: () => 'Корзина',
    [ROUTES.SUCCESS]: (state) => `Заказ #${state.cart.orderId}`,
    [ROUTES.ERROR]: () => 'Самокат',
    [ROUTES.NO_AREA]: () => 'Адрес доставки',
    [ROUTES.ADDRESS_FULL_ALL]: () => 'Адрес доставки и телефон',
    [ROUTES.ADDRESS_SIMPLE_QUESTION]: () => 'Адрес доставки',
    [ROUTES.ADDRESS_SIMPLE_GUIDE]: () => 'Адрес доставки',
    [ROUTES.ADDRESS_SIMPLE]: () => 'Адрес доставки',
    [ROUTES.ADDRESS_MAP]: () => 'Адрес доставки',
    [ROUTES.STATUS_SUMMARY]: getOrderStatusTitle,
    [ROUTES.STATUS_DETAILS]: getOrderStatusTitle,
    [ROUTES.STATUS_REPORT]: getOrderStatusTitle,
};

const formatDate = (date: Date) =>
    date.toLocaleString('ru-RU', {
        day: 'numeric',
        month: 'long',
        hour: 'numeric',
        minute: 'numeric',
    });

export function useHeaderAddress() {
    return useSelector((state: AppState) => state.userData.address);
}

export function useHeaderCart() {
    return useSelector((state: AppState) => ({
        count: state.cart.totalCount || 0,
        price: splitByThousands(Math.ceil(state.cart.totalPrice / 100)) || '0',
    }));
}

export function useHeaderTextContent() {
    const title = useSelector((state: AppState) => routeToTitleMap[selectPathname(state)]?.(state) ?? '');
    const subtitle = useSelector((state: AppState) => {
        const pathname = selectPathname(state);

        switch (pathname) {
            case ROUTES.STATUS_SUMMARY:
            case ROUTES.STATUS_DETAILS:
            case ROUTES.STATUS_REPORT: {
                if (state.orderStatusData.orderStatus !== OrderStatus.INITIAL) {
                    const { orderDate } = state.orderStatusData;
                    return orderDate ? formatDate(orderDate) : '';
                }

                return '';
            }

            case ROUTES.SUCCESS: {
                return formatDate(state.cart.date);
            }

            default: {
                return '';
            }
        }
    });

    return { title, subtitle };
}

interface HeaderVisibilityMaps {
    routeToVisibilityMap: Record<string, boolean>;
    routesToAddressVisibilityMap: Record<string, boolean>;
    routesToCartVisibilityMap: Record<string, boolean>;
}

export function useHeaderVisibilityFlags({
    routeToVisibilityMap,
    routesToAddressVisibilityMap,
    routesToCartVisibilityMap,
}: HeaderVisibilityMaps) {
    const isAddressVisible = useSelector(
        (state: AppState) => routesToAddressVisibilityMap[selectPathname(state)] || false,
    );
    const isHeaderVisible = useSelector(
        ({ router }: AppState) => routeToVisibilityMap[router.location.pathname] || false,
    );
    const isCartVisible = useSelector(
        (state: AppState) => state.cart.totalCount > 0 && routesToCartVisibilityMap[selectPathname(state)],
    );

    return {
        isAddressVisible,
        isHeaderVisible,
        isCartVisible,
    };
}
