import React from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { categoriesSelectItemAction, categoriesSwitchActiveItem } from 'store/actions/categories';

import { sortCategoriesByRank } from './Categories.helpers';
import { carouselFakeItemsOffset, carouselInitialIndex } from './Categories.constants';

function useItemsMap() {
    return useSelector(({ categoriesData }) => categoriesData.itemsMap);
}

export function useCategories() {
    const itemsMap = useItemsMap();

    return React.useMemo(() => {
        const categories = Object.values(itemsMap);

        categories.sort(sortCategoriesByRank);

        return categories;
    }, [itemsMap]);
}

export function useCategoriesCount() {
    const categories = useCategories();

    return categories.length + carouselFakeItemsOffset;
}

export function useCategoryItemImageUrlById(categoryId: string) {
    return useSelector(({ categoriesData }) => categoriesData.itemsMap[categoryId]?.imageUrl);
}

export function useActiveCategory() {
    return useSelector(({ categoriesData, categoriesPage }) => {
        if (!categoriesPage.activeItemId) {
            return null;
        }

        return categoriesData.itemsMap[categoriesPage.activeItemId] ?? null;
    });
}

export function useCarouselActiveIndex() {
    const activeCategory = useActiveCategory();
    const categories = useCategories();

    const activeIndex = categories.findIndex((category) => category.id === activeCategory?.id);

    return activeIndex > -1 ? activeIndex : carouselInitialIndex;
}

export function useSwitchCarouselActiveItem() {
    const dispatch = useDispatch();
    const categories = useCategories();

    return React.useCallback(
        (index: number) => {
            const activeItem = categories[index];

            if (!activeItem) {
                return;
            }

            dispatch(categoriesSwitchActiveItem(activeItem.id));
        },
        [dispatch, categories],
    );
}

export function useSelectCategoryHandler(id: string) {
    const dispatch = useDispatch();

    return React.useCallback(() => {
        dispatch(categoriesSelectItemAction(id));
    }, [dispatch, id]);
}
