import React from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullApartmentInputChangeAction } from 'store/actions/addressFullPage';

export const useFullAppartment = () => {
    const dispatch = useDispatch();

    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.apartmentInputValue);

    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullApartmentInputChangeAction(e.target.value));
        },
        [dispatch],
    );

    return { inputValue, handleInputChange };
};
