import React from 'react';
import { useSelector } from 'hooks/useSelector';

import { CatalogListPortal } from './List/CatalogList@portal';
import { CatalogSkeletonPortal } from './Skeleton/CatalogSkeleton@portal';

export const CatalogPortal: React.FC = () => {
    const order = useSelector(({ catalogPagePortal }) => catalogPagePortal.order);

    return order.length ? <CatalogListPortal /> : <CatalogSkeletonPortal />;
};
