import React from 'react';
import { push } from 'connected-react-router';
import { mapOrderStatusToCategory, OrderStatus, OrderStatusCategory } from 'store/constants/orderStatus';
import { ROUTES } from 'store/constants/router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';

export const useSummaryPage = () => {
    const dispatch = useDispatch();

    const orderStatus = useSelector((state) => state.orderStatusData.orderStatus);

    const orderStatusCategory = mapOrderStatusToCategory[orderStatus];

    const isLoaded = orderStatus !== OrderStatus.INITIAL;
    const isFailed = orderStatusCategory === OrderStatusCategory.FAILED;

    const handleDetailsClick = React.useCallback(() => {
        dispatch(push(ROUTES.STATUS_DETAILS));
    }, [dispatch]);

    const handleReportClick = React.useCallback(() => {
        dispatch(push(ROUTES.STATUS_REPORT));
    }, [dispatch]);

    return {
        orderStatus,
        orderStatusCategory,
        isLoaded,
        isFailed,
        handleDetailsClick,
        handleReportClick,
    };
};
