import React from 'react';
import { OrderStatus } from 'store/constants/orderStatus';
import {
    DialogPagePortal,
    DialogTitlePortal,
    DialogButtonPortal,
    DialogTextPortal,
} from 'components/DialogPage/DialogPage@portal';
import { SpinnerPage } from 'components/SpinnerPage/SpinnerPage';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import { useSummaryPage } from './StatusSummary.hooks';
import { StatusSummaryDecoration } from './Decoration/StatusSummaryDecoration';

const mapOrderStatusToSummaryText: Record<OrderStatus, string[]> = {
    [OrderStatus.INITIAL]: [''],
    [OrderStatus.INVOICE_CREATED]: ['Заказ создан,\n но не оплачен'],
    [OrderStatus.FUNDS_RESERVED]: ['Заказ создан\n и оплачен'],
    [OrderStatus.CONFIRMED_BY_PARTNER]: ['Собираем заказ.\n Курьер скоро\n направится к вам'],
    [OrderStatus.DELIVERING]: ['Заказ в пути.\n Курьер скоро\n будет у вас'],
    [OrderStatus.DELIVERED]: ['Заказ успешно\n доставлен'],
    [OrderStatus.CANCEL_BY_PARTNER]: ['К сожалению, заказ\n отменен'],
    [OrderStatus.CANCEL_BY_CUSTOMER]: ['К сожалению, заказ\n отменен'],
    [OrderStatus.CANCEL_BY_PAYMENT]: ['К сожалению, заказ\n отменен'],
    [OrderStatus.PAID]: ['Заказ успешно\n доставлен'],
    [OrderStatus.CANCELLED]: ['К сожалению, заказ\n отменен'],
    [OrderStatus.ERROR]: ['Ошибка обработки\n заказа'],
};

export const StatusSummaryPortal: React.FC = () => {
    const {
        orderStatus,
        orderStatusCategory,
        isLoaded,
        isFailed,
        handleDetailsClick,
        handleReportClick,
    } = useSummaryPage();

    const [title, text] = React.useMemo(() => mapOrderStatusToSummaryText[orderStatus], [orderStatus]);

    const buttons: React.ReactNode = React.useMemo(() => {
        const result = [
            <DialogButtonPortal shouldFocusOnMount={!isFailed} key="dialog-button-details" onClick={handleDetailsClick}>
                Детали
            </DialogButtonPortal>,
            <DialogButtonPortal
                shouldFocusOnMount={isFailed}
                key="dialog-button-report"
                view={isFailed ? ButtonViews.primary : ButtonViews.secondary}
                onClick={handleReportClick}
            >
                Связаться с нами
            </DialogButtonPortal>,
        ];

        return isFailed ? result.reverse() : result;
    }, [handleDetailsClick, handleReportClick, isFailed]);

    const decorations = <StatusSummaryDecoration orderStatusCategory={orderStatusCategory} />;

    if (!isLoaded) {
        // TODO: Заменить на скелет https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-243
        return <SpinnerPage text="Обновляем статус заказа" />;
    }

    return (
        <DialogPagePortal buttons={buttons} decorations={decorations}>
            <DialogTitlePortal>
                <MultilineText text={title} />
            </DialogTitlePortal>
            {text ? (
                <DialogTextPortal>
                    <MultilineText text={text} />
                </DialogTextPortal>
            ) : null}
        </DialogPagePortal>
    );
};
