import { produce } from 'immer';
import { History } from 'history';
import { AppReducer, initialState, AppBaseAction, AppState } from 'store/constants';
import { twoGisSuggestsData } from 'store/reducers/data/twoGisSuggestsData';
import { categoriesData } from 'store/reducers/data/categories';
import { productsData } from 'store/reducers/data/products';
import { productsDiffData } from 'store/reducers/data/productsDiff';
import { userData } from 'store/reducers/data/user';
import { deliveryData } from 'store/reducers/data/delivery';
import { characterData } from 'store/reducers/data/character';
import { itemDetailsModel } from 'store/reducers/model/itemDetails';
import { errorModel } from 'store/reducers/model/error';
import { orderStatusData } from 'store/reducers/data/orderStatus';
import { getRouterModel } from 'store/reducers/model/router';
import { cartModel } from 'store/reducers/model/cart';
import { userModel } from 'store/reducers/model/user';
import { categoriesModel } from 'store/reducers/model/categories';
import { catalogPageView } from 'store/reducers/view/catalogPage';
import { catalogPageTouchView } from 'store/reducers/view/catalogPage@touch';
import { catalogPagePortalView } from 'store/reducers/view/catalogPage@portal';
import { cartPageView } from 'store/reducers/view/cartPage';
import { cartPageTouchView } from 'store/reducers/view/cartPage@touch';
import { addressSimplePageView } from 'store/reducers/view/addressSimplePage';
import { addressFullPageView } from 'store/reducers/view/addressFullPage';
import { statusPageView } from 'store/reducers/view/statusPage';
import { DeviceFamily, deviceFamily } from 'tools/environment';

export const getRootReducer = (history: History, recoveryState?: AppState) => {
    const routerModel = getRouterModel(history);

    return (state = recoveryState || initialState, action: AppBaseAction) =>
        produce(state, (draft) => {
            const applyReducer = (reducer: AppReducer) => reducer(draft, action);

            [
                // уровень данных
                twoGisSuggestsData,
                categoriesData,
                productsData,
                productsDiffData,
                userData,
                deliveryData,
                characterData,
                orderStatusData,
            ].forEach(applyReducer);

            [
                // уровень бизнес логики
                routerModel,
                cartModel,
                userModel,
                errorModel,
                itemDetailsModel,
                categoriesModel,
            ].forEach(applyReducer);

            // уровень пропсов для для отрисовки компонентов
            if (deviceFamily === DeviceFamily.MOBILE) {
                [
                    // мобильная версия
                    cartPageTouchView,
                    catalogPageTouchView,
                    addressFullPageView,
                    statusPageView,
                ].forEach(applyReducer);
            } else if (deviceFamily === DeviceFamily.PORTAL) {
                [
                    // портальная версия
                    catalogPagePortalView,
                    cartPageView,
                    addressSimplePageView,
                    addressFullPageView,
                    statusPageView,
                ].forEach(applyReducer);
            } else {
                [
                    // десктопная версия
                    catalogPageView,
                    cartPageView,
                    addressSimplePageView,
                    addressFullPageView,
                    statusPageView,
                ].forEach(applyReducer);
            }
        });
};
