import React from 'react';
import styled from 'styled-components';
import { HeaderSberbox } from 'components/Header/Header@sberbox';
import { layout } from 'style/const/layout@sberbox';

import { AppLayoutCommon, AppContentCommon } from './AppLayout';

const AppContentSberbox = styled(AppContentCommon)`
    height: ${layout.contentHeight}px;
`;

export const AppLayoutSberbox: React.FC = ({ children }) => {
    const renderHeader = React.useCallback(() => <HeaderSberbox />, []);
    const renderContent = React.useCallback(() => <AppContentSberbox>{children}</AppContentSberbox>, [children]);

    return <AppLayoutCommon renderHeader={renderHeader} renderContent={renderContent} />;
};
