import React from 'react';
import styled from 'styled-components';
import { ButtonSize, ButtonTextAlign, ButtonViews } from 'components/Button/Button';

import {
    useAddressName,
    useHandleClick,
    useHandleFocus,
    useIsItemDisabled,
    useIsItemFlashing,
} from './AddressSimpleSuggestItem.hooks';
import { StyledButtonCommon } from './AddressSimpleSuggestItem';

const StyledButton = styled(StyledButtonCommon)`
    padding: 24px 40px;
`;

interface SuggestListItemProps {
    id: string;
    shouldFocusOnMount?: boolean;
}

export const AddressSimpleSuggestListItemPortal: React.FC<SuggestListItemProps> = ({ id, shouldFocusOnMount }) => {
    const addressName = useAddressName(id);
    const isDisabled = useIsItemDisabled(id);
    const isFlashing = useIsItemFlashing(id);

    const handleClick = useHandleClick();
    const handleFocus = useHandleFocus(id);

    return (
        <StyledButton
            shouldFocusOnMount={shouldFocusOnMount}
            disabled={isDisabled && !isFlashing}
            isFlashing={isFlashing}
            onFocus={handleFocus}
            onClick={handleClick}
            view={ButtonViews.secondary}
            size={ButtonSize.s}
            textAlign={ButtonTextAlign.left}
        >
            {addressName}
        </StyledButton>
    );
};
