import { AppReducer } from 'store/constants';
import { Category, StoreCategory } from 'store/constants/categories';

const normalizeCategory = (category: Category): StoreCategory => ({
    id: category.id,
    rank: category.rank,
    name: category.name || '',
    imageUrl: category.imageUrl[0] || '',
});

export const normalizeCategories = (categories: Category[]): Record<string, StoreCategory> =>
    categories.reduce<Record<string, StoreCategory>>((acc, category) => {
        acc[category.id] = normalizeCategory(category);

        return acc;
    }, {});

export const categoriesData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'SHOW_CATEGORIES': {
            state.categoriesData.itemsMap = normalizeCategories(action.payload.categories);
            break;
        }

        default:
            break;
    }
};
