import { MultilineText } from 'components/MultilineText/MultilineText';
import React from 'react';
import { goToAddressFullAllThunk } from 'store/thunks/native';
import styled from 'styled-components';
import { Footnote1, Button2 } from '@sberdevices/plasma-styles';
import { secondary } from '@sberdevices/plasma-tokens';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { makePurchaseAction } from 'store/actions/server';
import {
    CartInfoPinPortal,
    CartInfoContainerPortal,
    CartPersonalPortal,
    CartAddressPortal,
} from 'components/Cart/Info/CartInfo@portal';
import { Button, ButtonViews } from 'components/Button/Button';

const StyledAskPersonal = styled(Footnote1)`
    margin-top: 14px;

    text-align: center;

    color: ${secondary};
`;

const CartInfoButton = styled(Button)`
    margin-top: 60px;
    padding: 20px 32px;

    &[disabled] {
        opacity: 0.3;
    }
`;

const askPersonal = 'Чтобы сделать заказ, укажите номер телефона, \nквартиру и другую дополнительную \nинформацию';

export const CartInfoPortal: React.FC = () => {
    const dispatch = useDispatch();
    const { address, customerName } = useSelector(({ userData }) => userData);
    const {
        deliveryTime,
        isMakePurchaseButtonDisabled,
        makePurchaseButtonView,
        hasRequiredData,
        phoneNumber,
        personalAddress,
    } = useSelector(({ cartPage }) => cartPage);
    const handlePurchaseClick = React.useCallback(() => dispatch(makePurchaseAction()), [dispatch]);

    const handleSetAddressClick = React.useCallback(() => {
        dispatch(goToAddressFullAllThunk());
    }, [dispatch]);

    return (
        <CartInfoContainerPortal>
            <CartInfoPinPortal time={deliveryTime} />
            <CartAddressPortal>{address}</CartAddressPortal>
            {hasRequiredData ? (
                <>
                    <CartPersonalPortal address={personalAddress} phone={phoneNumber} name={customerName} />
                    <CartInfoButton
                        view={makePurchaseButtonView}
                        disabled={isMakePurchaseButtonDisabled}
                        shouldFocusOnMount
                        shouldLockAfterClick
                        onClick={handlePurchaseClick}
                    >
                        Перейти к оплате
                    </CartInfoButton>
                </>
            ) : (
                <>
                    <StyledAskPersonal>
                        <MultilineText text={askPersonal} />
                    </StyledAskPersonal>
                    <CartInfoButton view={ButtonViews.primary} shouldFocusOnMount onClick={handleSetAddressClick}>
                        <Button2>Продолжить</Button2>
                    </CartInfoButton>
                </>
            )}
        </CartInfoContainerPortal>
    );
};
