import React from 'react';
import { ROUTES } from 'store/constants/router';

import { HeaderBackButtonPortal } from './BackButton/HeaderBackButton@portal';
import { HeaderCommon, routesToCartVisibilityMapCommon, routeToVisibilityMapCommon } from './Header';

export const routeToVisibilityMapPortal: Record<string, boolean> = {
    ...routeToVisibilityMapCommon,
    [ROUTES.ITEM_DETAILS]: true,
};

const routesToCartVisibilityMapPortal: Record<string, boolean> = {
    ...routesToCartVisibilityMapCommon,
    [ROUTES.ITEM_DETAILS]: true,
};

export const HeaderPortal: React.FC = () => {
    const renderBackButton = React.useCallback(() => <HeaderBackButtonPortal />, []);

    return (
        <HeaderCommon
            renderBackButton={renderBackButton}
            routeToVisibilityMap={routeToVisibilityMapPortal}
            routesToCartVisibilityMap={routesToCartVisibilityMapPortal}
        />
    );
};
