import { useKeyboardClose } from 'hooks/useKeyboardClose';
import React from 'react';
import { addressFullValidationOkThunk } from 'store/thunks/addressFullPage';
import { useDispatch } from 'hooks/useDispatch';

export const useInputHandlers = () => {
    const dispatch = useDispatch();

    const handleKeyboardClose = React.useCallback(() => {
        dispatch(addressFullValidationOkThunk());
    }, [dispatch]);

    const { onInputFocus, onInputSubmit } = useKeyboardClose({
        handleKeyboardClose,
        handleInputSubmit: handleKeyboardClose,
    });

    return { handleInputFocus: onInputFocus, handleInputSubmit: onInputSubmit };
};
