import { CarouselContext, Slider } from 'pure-react-carousel';
import React from 'react';
import styled from 'styled-components';
import { StoreCategory } from 'store/constants/categories';

import { useCategories, useSwitchCarouselActiveItem } from '../Categories.hooks';
import { CategoriesSlide } from '../Slide/CategoriesSlide';

const StyledSlider = styled(Slider)`
    overflow: visible;
`;

const prepareItemIds = (categories: StoreCategory[]) => {
    const items = Array.from(Array(4));

    items.splice(2, 0, ...categories);

    return items.map((item, index) => (item ? item.id : `fake-slide-${index}`));
};

export const CategoriesSlider: React.FC = () => {
    const categories = useCategories();
    const carouselContext = React.useContext(CarouselContext);
    const switchActiveItem = useSwitchCarouselActiveItem();

    React.useEffect(() => {
        function onChange() {
            switchActiveItem(carouselContext.state.currentSlide);
        }

        carouselContext.subscribe(onChange);

        return () => carouselContext.unsubscribe(onChange);
    }, [carouselContext, switchActiveItem]);

    const content = React.useMemo(
        () =>
            prepareItemIds(categories).map((itemId, index) => (
                <CategoriesSlide key={itemId} itemId={itemId} index={index} />
            )),
        [categories],
    );

    return <StyledSlider>{content}</StyledSlider>;
};
