import { createAction } from 'tools/create-action';

interface AddressFullPhoneNumberInputChangePayload {
    inputType: InputEvent['inputType'];
    data: InputEvent['data'];
}

/* Изменить телефон */
export const addressFullPhoneNumberInputChangeAction = (payload: AddressFullPhoneNumberInputChangePayload) =>
    createAction('addressFull/PHONE_NUMBER_INPUT_CHANGE', payload);

/* Изменить квартиру */
export const addressFullApartmentInputChangeAction = (payload: string) =>
    createAction('addressFull/APARTMENT_INPUT_CHANGE', payload);

/* Изменить подъезд */
export const addressFullEntranceInputChangeAction = (payload: string) =>
    createAction('addressFull/ENTRANCE_INPUT_CHANGE', payload);

/* Изменить этаж */
export const addressFullFloorInputChangeAction = (payload: string) =>
    createAction('addressFull/FLOOR_INPUT_CHANGE', payload);

/* Изменить комментарий */
export const addressFullDoorCodeInputChangeAction = (payload: string) =>
    createAction('addressFull/DOORCODE_INPUT_CHANGE', payload);
