import { goBack } from 'connected-react-router';
import React from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { useAddressSimpleUpdateSuggests } from 'pages/AddressSimple/AddressSimple.hooks';
import { buildAddressString } from 'pages/AddressSimple/AddressSimple.tools';
import { addressInputDeclineThunk, addressSubmitThunk } from 'store/thunks/native';
import { TwoGisSuggestFetchingStatus } from 'store/reducers/data/twoGisSuggestsData';

function useInputValue() {
    return useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
}

function useAddressSubmitting() {
    return useSelector(({ addressSimplePage }) => addressSimplePage.isAddressSubmitting);
}

function useSuggest() {
    return useSelector(({ twoGisSuggestsData: { mostRepresentationSuggest } }) => mostRepresentationSuggest);
}

function useSuggestFetchPending() {
    const suggestFetchingStatus = useSelector(({ twoGisSuggestsData }) => twoGisSuggestsData.fetchingStatus);

    // саджесты еще не приехали, показываем пустой экран, потому рендерим скелетон
    return suggestFetchingStatus === TwoGisSuggestFetchingStatus.PENDING;
}

export function useTryAgain() {
    const inputValue = useInputValue();
    const dispatch = useDispatch();

    const handleRepeatClick = React.useCallback(() => {
        dispatch(goBack());
    }, [dispatch]);

    return { inputValue, handleRepeatClick };
}

export function useAddressFound() {
    const isAddressSubmitting = useAddressSubmitting();
    const suggest = useSuggest();
    const dispatch = useDispatch();

    const handleAcceptButtonClick = React.useCallback(() => {
        dispatch(addressSubmitThunk());
    }, [dispatch]);

    const handleDeclineButtonClick = React.useCallback(() => {
        dispatch(addressInputDeclineThunk());
    }, [dispatch]);

    const addressName = React.useMemo(() => suggest && buildAddressString(suggest), [suggest]);

    return { addressName, isAddressSubmitting, handleAcceptButtonClick, handleDeclineButtonClick };
}

export function useAddressSimpleQuestion() {
    const isSuggestFetchPenging = useSuggestFetchPending();
    const suggest = useSuggest();

    useAddressSimpleUpdateSuggests();

    return { isSuggestFetchPenging, suggest };
}
