export const getGeolocation = (timeout = 2000) =>
    new Promise<Position>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, {
            maximumAge: 5 * 60 * 1000,
            timeout,
            enableHighAccuracy: false,
        });

        // что-то вычисление текущей позиции не всегда по таймауту падает,
        // так что продублируем руками
        setTimeout(() => {
            reject();
        }, timeout);
    });
