import React from 'react';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useInputHandlers } from 'hooks/useAddressInputHandlers';

import { useFullEntrance } from './AddressFullEntrance.hooks';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullEntrance = () => {
    const { handleInputFocus, handleInputSubmit } = useInputHandlers();

    const { inputValue, handleInputChange } = useFullEntrance();

    return (
        <StyledInput
            title="Подъезд"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
            onInputFocus={handleInputFocus}
        />
    );
};
