import React from 'react';
import { useSelector } from 'hooks/useSelector';
import {
    CartListContainerSberbox,
    CartListScrollerSberbox,
    CartTotalPriceSberbox,
} from 'components/Cart/List/CartList@sberbox';

import { StatusDetailsListItemSberbox } from '../ListItem/StatusDetailsListItem@sberbox';

export const StatusDetailsListSberbox: React.FC = () => {
    const { order, wrapperYShift, topGradientVisible, bottomGradientVisible, totalPrice } = useSelector(
        ({ orderStatusDetailsPage }) => orderStatusDetailsPage,
    );

    const itemList = React.useMemo(
        () =>
            order.map((itemId, index, list) => (
                <StatusDetailsListItemSberbox
                    key={itemId}
                    canFocus={index > 0 && index < list.length - 2}
                    index={index + 1}
                    id={itemId}
                />
            )),
        [order],
    );

    return (
        <CartListContainerSberbox>
            {order.length > 0 && (
                <CartListScrollerSberbox
                    topGradientVisible={topGradientVisible}
                    bottomGradientVisible={bottomGradientVisible}
                    wrapperYShift={wrapperYShift}
                >
                    {itemList}
                </CartListScrollerSberbox>
            )}
            <CartTotalPriceSberbox>Сумма заказа {totalPrice} ₽</CartTotalPriceSberbox>
        </CartListContainerSberbox>
    );
};
