import React from 'react';
import {
    DialogPageSberbox,
    DialogTitleSberbox,
    DialogButtonSberbox,
    DialogTextSberbox,
} from 'components/DialogPage/DialogPage@sberbox';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import { StatusEmptyDecoration } from './Decoration/StatusEmptyDecoration';
import { useStatusEmptyPage } from './StatusEmpty.hooks';

export const StatusEmptySberbox: React.FC = () => {
    const { handleCloseClick } = useStatusEmptyPage();

    const buttons = React.useMemo(
        () => [
            <DialogButtonSberbox shouldFocusOnMount view={ButtonViews.secondary} onClick={handleCloseClick}>
                Не сейчас
            </DialogButtonSberbox>,
        ],
        [handleCloseClick],
    );

    const decorations = <StatusEmptyDecoration />;

    return (
        <DialogPageSberbox buttons={buttons} decorations={decorations}>
            <DialogTitleSberbox>
                <MultilineText text="Заказов пока нет" />
            </DialogTitleSberbox>
            <DialogTextSberbox>
                <MultilineText text="Чтобы сделать заказ, скажите «Хочу попкорн»" />
            </DialogTextSberbox>
        </DialogPageSberbox>
    );
};
