import React from 'react';
import styled, { StyleableComponent } from 'styled-components';
import { primary, secondary } from '@sberdevices/plasma-tokens';
import { ButtonViews, Button, ButtonSize, ButtonProps } from 'components/Button/Button';
import { layoutContentHeightVar } from 'components/GlobalLayoutVars/GlobalLayoutVars';

const buttonsGap = 16;

const dialogPageVarPrefix = '--dialog-page';

export const dialogPagePaddingLeftVar = `${dialogPageVarPrefix}-padding-left`;
export const dialogPageMarginTopVar = `${dialogPageVarPrefix}-margin-top`;
export const dialogPageButtonsMarginTopVar = `${dialogPageVarPrefix}-buttons-margin-top`;

const StyledContainer = styled.div`
    position: relative;

    height: var(${layoutContentHeightVar});
    padding-left: var(${dialogPagePaddingLeftVar}, 'none');
    margin-top: var(${dialogPageMarginTopVar}, 'none');
`;

const StyledContent = styled.div`
    position: relative;
    height: var(${layoutContentHeightVar});
    z-index: 2;

    display: flex;
    flex-direction: column;
    justify-content: center;

    align-content: left;
`;

export const styledDialogTitle = (component: StyleableComponent) => styled(component)`
    color: ${primary};
`;

export const styledDialogText = (component: StyleableComponent) => styled(component)`
    margin-top: 24px;

    min-height: 72px;

    white-space: pre-wrap;

    color: ${secondary};
`;

const StyledButtonContainer = styled.div`
    margin-left: ${-0.5 * buttonsGap}px;
    margin-top: var(${dialogPageButtonsMarginTopVar}, 0);

    display: flex;
    flex-direction: row;
`;

const StyledButton = styled(Button)`
    margin: 0 ${0.5 * buttonsGap}px;
`;

const StyledDecorations = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    width: 100%;
    height: 100%;
`;

export interface DialogPageCommonProps {
    buttons?: React.ReactNode;
    decorations?: React.ReactNode;
}

export const DialogPageCommon: React.FC<DialogPageCommonProps> = ({ children, buttons, decorations, ...props }) => {
    return (
        <StyledContainer {...props}>
            <StyledContent>
                {children}
                {buttons && <StyledButtonContainer>{buttons}</StyledButtonContainer>}
            </StyledContent>
            <StyledDecorations>{decorations}</StyledDecorations>
        </StyledContainer>
    );
};

export const DialogButton: React.FC<ButtonProps> = ({
    children,
    size = ButtonSize.s,
    view = ButtonViews.secondary,
    ...props
}) => (
    <StyledButton size={size} view={view} {...props}>
        {children}
    </StyledButton>
);
