import React from 'react';
import styled, { css, FlattenSimpleInterpolation } from 'styled-components';
import { Caption, Footnote2, Underline } from '@sberdevices/plasma-styles';
import { colors, secondary } from '@sberdevices/plasma-tokens';
import { transition } from 'style/const/transition';

type IndexBadgeSize = 'xxs' | 'xs' | 's' | 'm';

interface StyledIndexBadgeProps {
    visible: boolean;
    size: IndexBadgeSize;
}

const mapSizeToPx: Record<IndexBadgeSize, number> = {
    xxs: 22,
    xs: 32,
    s: 44,
    m: 64,
};

const mapSizeToParams: Record<IndexBadgeSize, FlattenSimpleInterpolation> = {
    xxs: css`
        width: ${mapSizeToPx.xxs}px;
        height: ${mapSizeToPx.xxs}px;
    `,
    xs: css`
        width: ${mapSizeToPx.xs}px;
        height: ${mapSizeToPx.xs}px;
    `,
    s: css`
        width: ${mapSizeToPx.s}px;
        height: ${mapSizeToPx.s}px;
    `,
    m: css`
        width: ${mapSizeToPx.m}px;
        height: ${mapSizeToPx.m}px;
    `,
};

const StyledIndexBadge = styled.div<StyledIndexBadgeProps>`
    position: absolute;

    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    ${({ size }) => mapSizeToParams[size]};

    color: ${secondary};

    transition: transform ${transition.default}, opacity ${transition.default};

    background: ${colors.blackSecondary};

    ${({ visible }) => css`
        transform: scale(${Number(visible)});
        opacity: ${Number(visible)};
    `}
`;

interface IndexBadgeProps {
    size: IndexBadgeSize;
    index?: number;
}

export const IndexBadge: React.FC<IndexBadgeProps> = ({ index, size, ...props }) => {
    return (
        <StyledIndexBadge visible={index !== undefined && index > 0} size={size} {...props}>
            {size === 'xxs' && <Underline>{index}</Underline>}
            {size === 'xs' && <Footnote2>{index}</Footnote2>}
            {size === 's' && <Caption>{index}</Caption>}
            {size === 'm' && <Footnote2>{index}</Footnote2>}
        </StyledIndexBadge>
    );
};
