import React from 'react';
import { ButtonViews } from 'components/Button/Button';
import {
    LocationButtonSberbox,
    LocationMapBackgroundSberbox,
    LocationPageSberbox,
    LocationTextSberbox,
    LocationTitleSberbox,
} from 'components/LocationPage/LocationPage@sberbox';

import { AddressFoundPin, TryAgainPin } from './AddressSimpleQuestion';
import { useAddressFound, useAddressSimpleQuestion, useTryAgain } from './AddressSimpleQuestion.hooks';

const TryAgain: React.FC = () => {
    const { inputValue, handleRepeatClick } = useTryAgain();

    const buttons = React.useMemo(
        () => (
            <LocationButtonSberbox shouldFocusOnMount view={ButtonViews.secondary} onClick={handleRepeatClick}>
                Ввести еще раз
            </LocationButtonSberbox>
        ),
        [handleRepeatClick],
    );

    const background = React.useMemo(() => <LocationMapBackgroundSberbox />, []);

    return (
        <LocationPageSberbox buttons={buttons} background={background}>
            <TryAgainPin />
            <LocationTitleSberbox>{inputValue}</LocationTitleSberbox>
            <LocationTextSberbox>Этот адрес не найден, попробуйте еще раз</LocationTextSberbox>
        </LocationPageSberbox>
    );
};

const AddressFound: React.FC = () => {
    const { addressName, isAddressSubmitting, handleAcceptButtonClick, handleDeclineButtonClick } = useAddressFound();

    const buttons = React.useMemo(
        () => (
            <>
                <LocationButtonSberbox
                    shouldFocusOnMount
                    shouldLockAfterClick
                    view={ButtonViews.primary}
                    onClick={handleAcceptButtonClick}
                >
                    Да, все верно
                </LocationButtonSberbox>
                <LocationButtonSberbox
                    view={ButtonViews.secondary}
                    onClick={handleDeclineButtonClick}
                    disabled={isAddressSubmitting}
                >
                    Нет, выбрать другой
                </LocationButtonSberbox>
            </>
        ),
        [handleAcceptButtonClick, handleDeclineButtonClick, isAddressSubmitting],
    );

    return (
        <LocationPageSberbox buttons={buttons}>
            <AddressFoundPin />
            <LocationTitleSberbox>{addressName}</LocationTitleSberbox>
            <LocationTextSberbox>Это ваш адрес?</LocationTextSberbox>
        </LocationPageSberbox>
    );
};

export const AddressSimpleQuestionSberbox: React.FC = () => {
    const { isSuggestFetchPenging, suggest } = useAddressSimpleQuestion();

    if (isSuggestFetchPenging) {
        return <LocationPageSberbox />;
    }

    if (!suggest) {
        return <TryAgain />;
    }

    return <AddressFound />;
};
